$grey1: #333333;
$pink: #f6f0e1;
$white: #ffffff;
$yellow: #f7ca59;
$blue: #a6d7c9;
$bloomarin: #bae8db;


.section_title {
  font: normal 58px Playfair Display;
  line-height: 72px;
  margin-top: 130px;
  text-align: center;
  text-transform: capitalize;
  color: $grey1;

  @media screen and (min-width: 424px) and (max-width: 992px) {
    font-size: 30.8px;
    line-height: 45px;
  }
  
  @media screen and (min-width: 320px) and (max-width: 424px) { 
    font-size: 26.4px;
  }
}



.audits {
  position: relative;
  h2 {
    @media screen and (max-width: 768px) {
      margin-top: 50px;
    }
  }
  .audits_body {
    position: relative;
    margin: 94px auto 280px;
    @media screen and (max-width: 992px) {
      margin: 35px auto 85px;
    }
    .audits_item {
      margin-bottom: 65px;
      @media screen and (max-width: 768px) {
        margin-bottom: 30px;
      }
      .audits_head {
        margin-bottom: 20px;

        img {
          margin-right: 20px;

          @media screen and (max-width: 768px) {
            margin-right: 10px;
            transform: scale(0.8);
          }
        }

        h4 {
          font: 900 24px niveau-grotesk;
          line-height: 48px;
          text-transform: uppercase;
          color: $grey1;

          @media screen and (max-width: 768px) {
            font-size: 22.4px;
            line-height: 30px;
          }
        }
      }

      p {
        font: 500 20px niveau-grotesk;
        line-height: 28px;
        letter-spacing: 0.3px;
        color: $grey1;

        @media screen and (max-width: 768px) {
          font-size: 14.04px;
          line-height: 20px;
        }
      }
    }

    .check_body {
      z-index: 2;
      bottom: -100%;
      position: absolute;
      width: 1120px;
      height: 417px;
      background: #f7ca59;
      border-radius: 20px;
      margin: 0 auto;
      padding: 80px 0 0 81px;

      @media screen and (min-width: 992px) and (max-width: 1200px) {
        width: 97%;
        padding: 80px 0 0 40px;
      }

      @media screen and (min-width: 768px) and (max-width: 992px) {
        width: 97%;
        position: relative;
        padding: 40px 0 0 40px;
        height: auto;
      }

      @media screen and (min-width: 320px) and (max-width: 768px) {
        width: 97%;
        position: relative;
        padding: 40px 0 0 0px;
        height: auto;
      }

      h3 {
        font: 900 40.64px niveau-grotesk;
        line-height: 51px;
        text-transform: uppercase;
        color: $grey1;
        margin-bottom: 24px;

        @media screen and (max-width: 768px) {
          font-size: 22.4px;
          line-height: 30px;
          text-align: center;
        }
      }

      ul {
        @media screen and (min-width: 430px) and (max-width: 768px) {
          padding: 30px 0 28px 11%;
        }
        @media screen and (min-width: 320px) and (max-width: 430px) {
          padding: 0 0 0 11%;
        }
        li {
          font: 500 24px niveau-grotesk;
          line-height: 32px;
          letter-spacing: 0.3px;
          color: $grey1;
          margin-bottom: 20px;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            font-size: 14.04px;
            line-height: 20px;
            margin-bottom: 15px;
          }
        }
      }
      img {
        position: absolute;
        right: 0;
        bottom: 0;

        @media screen and (min-width: 936px) and (max-width: 1120px) {
          max-width: 500px;
        }
        @media screen and (min-width: 768px) and (max-width: 936px) {
          max-width: 333px;
        }
        @media screen and (min-width: 430px) and (max-width: 768px) {
          width: 45%;
        }
        @media screen and (min-width: 320px) and (max-width: 430px) {
          width: 70%;
          margin: 0 auto;
          position: relative;
          display: block;
        }
      }
    }
    .dots {
      position: absolute;
      left: -10%;
      bottom: -36%;

      @media screen and (max-width: 992px) {
        bottom: 0%;
      }
    }
  }
}

