.points {
  position: relative;
  background: $blue;
  padding-bottom: 160px;
  .stairs {
    position: absolute;
    width: 100%;
    top: 3px;
    .stairs_item {
      display: block;
      background-color: $pink;
      margin-bottom: 6px;
    }
    .stairs_item-two {
      height: 2px;
    }
    .stairs_item-four {
      height: 4px;
    }
    .stairs_item-eight {
      height: 8px;
    }
    .stairs_item:last-child {
      margin-bottom: 0;
    }
  }
  @media (max-width: 768px) {
    padding-bottom: 60px;
  }
  h2 {
    font: normal 58px Playfair Display;
    line-height: 72px;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    padding-top: 167px;
    width: 90%;
    margin: 0 auto 75px;
    @media (max-width: 992px) {
      font-size: 30.8px;
      line-height: 45px;
    }
    @media (max-width: 768px) {
      padding-top: 120px;
     
      line-height: 32px;
      margin: 0 auto 50px;
    }
    @media (max-width: 425px) {
      font-size: 26.4px;
    }
  }

  .points_item {
    @media (max-width: 767px) {
      padding-left: 8%;
    }
    .points_item__header {
      flex-direction: column;

      @media (max-width: 768px) {
        flex-direction: row;
        justify-content: center;
        align-items: center;
      }

      .points_image {
        width: 61px;
        height: 90px;
        margin: 0 0 45px;
        @media (max-width: 768px) {
          transform: scale(0.8);
          margin: 0;
        }

        img {
          @media (max-width: 768px) {
            max-width: 85px;
          }
        }
      }

      h4 {
        font: 900 32px niveau-grotesk;
        line-height: 48px;
        text-transform: uppercase;
        color: $grey1;
        margin-bottom: 16px;

        @media (max-width: 768px) {
          font-size: 22.4px;
          line-height: 32px;
          margin: 0 0 0 20px;
        }
      }
    }
    ul {
      padding-left: 20px;

      li {
        font: 500 24px niveau-grotesk;
        line-height: 32px;
        letter-spacing: 0.3px;
        color: $grey1;
        margin-bottom: 15px;

        @media (max-width: 768px) {
          font-size: 14.04px;
          line-height: 20px;
        }
      }
    }
    .list_first {
      @media screen and (min-width: 769px) and (max-width: 948px) {
        margin-top: 50px;
      }
    }
    .list_last {
      @media screen and (min-width: 769px) and (max-width: 948px) {
        margin-top: 45px;
      }
    }
  }
}

.source {
  padding: 160px 0;
  @media (max-width: 768px) {
    padding: 100px 0;
  }

  .source_body {
    position: relative;
    width: 1120px;
    height: 338px;
    background: $pink;
    border-radius: 20px;
    margin: 0 auto;
    @media screen and (max-width: 1160px) {
      width: 98%;
    }
    @media (max-width: 768px) {
      height: auto;
      padding-bottom: 70px;
    }

    .icon {
      left: 64px;
      top: -63px;
      position: absolute;

      @media screen and (max-width: 768px) {
        transform: scale(0.8);
        left: 17px;
      }
    }
    .source_title {
      padding: 80px 0 0 64px;
      @media screen and (max-width: 992px) {
        padding: 80px 0 0 15px;
      }
      h2 {
        font: 900 32px niveau-grotesk;
        line-height: 48px;
        text-transform: uppercase;
        color: #000000;

        @media (max-width: 768px) {
          font-size: 20.4px;
          line-height: 30px;
          width: 90%;
        }
        @media (max-width: 425px) {
          font-size: 20.4px;
          line-height: 30px;
          width: 90%;
          margin-bottom: 2rem;
        }
      }
    }

    .source_confirm {
      padding: 0 0 0 35px;
      @media screen and (max-width: 992px) {
        padding: 0;
      }

      h4 {
        font: 900 24px niveau-grotesk;
        line-height: 32px;
        letter-spacing: 0.3px;
        color: $grey1;
        margin: 80px 0 40px;
        @media screen and (min-width: 576px) and (max-width: 768px) {
          font-size: 20.4px;
          line-height: 20px;
          width: 95%;
          padding-left: 15px;
        }
        @media screen and (min-width: 320px) and (max-width: 576px) {
          font-size: 20.4px;
          line-height: 20px;
          width: 95%;
          padding-left: 15px;
          margin-top: 0;
        }
      }
      a {
        padding: 15px 75px;
        box-shadow: 4px 4px #333;
        background: #fc6839;
        border: 4px solid $grey1;
        box-sizing: border-box;
        border-radius: 10px;
        font: 900 20px niveau-grotesk;
        line-height: 28px;
        text-align: center;
        letter-spacing: 0.3px;
        color: #ffffff;
        cursor: pointer;
        @media (max-width: 768px) {
          font-size: 14.04px;
          line-height: 20px;
          padding: 15px 35px;
          margin-left: 15px;
        }
      }
    }
    .dots {
      z-index: -1;
      right: -12%;
      bottom: -34%;
      position: absolute;
      @media (max-width: 1400px) {
        right: 1%;
      }
      @media (max-width: 768px) {
        bottom: -11%;
        right: 7%;
      }
    }
  }
}

.assessment {
  h2 {
    font: normal 58px Playfair Display;
    line-height: 72px;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    padding-top: 130px;
    width: 90%;
    margin: 0 auto 64px;
    @media (max-width: 992px) {
      padding: 0;
      font-size: 30.6px;
      line-height: 45px;
      margin-bottom: 45px;
    }
    @media (max-width: 768px) {
      margin: 0 auto;
      padding-top: 30px;
      width: 100%;
      line-height: 40px;
    }
    @media (max-width: 425px) {
      font-size: 26.4;
    }
  }

  .assessment_body {
    position: relative;
    margin-top: 111px;
    padding-bottom: 160px;
    @media (max-width: 992px) {
      margin-top: 0px;
    }
    @media (max-width: 768px) {
      margin-top: 50px;
    }

    .assessment_item {
      position: relative;
      margin-bottom: 100px;
      @media (max-width: 768px) {
        margin-bottom: 45px;
      }
    }

    h5 {
      font: 500 24px niveau-grotesk;
      line-height: 30px;
      letter-spacing: 0.3px;
      color: $grey1;
      width: 95%;

      @media screen and (min-width: 320px) and (max-width: 991px) {
        margin: 0 auto;
      }
      @media screen and (max-width: 768px) {
        font-size: 14.04px;
        line-height: 20px;
        margin: 0 auto;
      }
    }

    .list {
      width: 90%;
      margin-top: 32px;
      padding-left: 25px;
      @media screen and (min-width: 320px) and (max-width: 991px) {
        margin: 32px auto 0;
      }

      .list_item {
        list-style: none;
        margin-bottom: 20px;
        @media (max-width: 768px) {
          margin-bottom: 10px;
        }
        position: relative;
        &:nth-child(2),
        &:nth-child(3) {
          &:after {
            content: "";
            position: absolute;
            top: 50%;
            left: -25px;
            transform: translateY(-50%);
            width: 20px;
            height: 3px;
            background-color: #000000;
            @media (max-width: 768px) {
              height: 2px;
            }
          }
        }
        .list_img {
          margin-right: 13px;

          @media (max-width: 768px) {
            transform: scale(0.8);
          }
        }
        .list_title {
          font: normal 22px niveau-grotesk;
          line-height: 32px;
          letter-spacing: 0.3px;
          color: $grey1;

          @media (max-width: 768px) {
            font-size: 14.04px;
          }

          br {
            @media (max-width: 768px) {
              display: none;
            }
          }
        }
      }
    }
    .list_line {
      position: absolute;
      top: 151px;
      bottom: 8%;
      left: 0;
      width: 20px;
      border-radius: 7px 0 0 7px;
      border-left: 3px solid #000;
      border-top: 3px solid #000;
      border-bottom: 3px solid #000;
      @media screen and (min-width: 1088px) and (max-width: 1121px) {
        top: 42%;
      }
      @media screen and (min-width: 991px) and (max-width: 1088px) {
        top: 42%;
        bottom: 7%;
      }
      @media screen and (min-width: 819px) and (max-width: 991px) {
        left: 5%;
        top: 37%;
        bottom: 4%;
      }

      @media screen and (min-width: 768px) and (max-width: 819px) {
        left: 5%;
        top: 44%;
        bottom: 4%;
      }
      @media screen and (min-width: 627px) and (max-width: 768px) {
        left: 5%;
        top: 38%;
        bottom: 6%;
        border-left: 2px solid #000;
        border-top: 2px solid #000;
        border-bottom: 2px solid #000;
      }
      @media screen and (min-width: 529px) and (max-width: 627px) {
        left: 5%;
        top: 32%;
        bottom: 12%;
        border-left: 2px solid #000;
        border-top: 2px solid #000;
        border-bottom: 2px solid #000;
      }

      @media screen and (min-width: 320px) and (max-width: 529px) {
        left: 5%;
        top: 36%;
        bottom: 29px;
        border-left: 2px solid #000;
        border-top: 2px solid #000;
        border-bottom: 2px solid #000;
      }
    }
    .text {
      h4 {
        font: 900 20px niveau-grotesk;
        line-height: 28px;
        letter-spacing: 0.3px;
        color: $grey1;
        @media (max-width: 992px) {
          width: 95%;
          margin: 0 auto;
        }
        @media (max-width: 768px) {
          width: 95%;
          margin: 0 auto;
          font-size: 14.04px;
          line-height: 20px;
        }
      }
    }
    .poster {
      position: absolute;
      width: 373px;
      height: 461px;
      background: #a6d7c9;
      border-radius: 30px;
      border-bottom-right-radius: 45px;
      right: 0;
      bottom: -50px;
      z-index: 2;

      @media screen and (min-width: 991px) and (max-width: 1400px) {
        right: 10%;
      }
      @media screen and (min-width: 768px) and (max-width: 991px) {
        position: relative;
        margin: 40px auto 0;
      }
      @media screen and (min-width: 500px) and (max-width: 768px) {
        position: relative;
        margin: 40px auto 0;
        height: 360px;
      }
      @media screen and (min-width: 320px) and (max-width: 500px) {
        position: relative;
        margin: 0 auto;
        height: 227px;
        width: 267px;
      }
      .computer {
        right: -95px;
        top: -60px;
        position: absolute;
        @media screen and (min-width: 500px) and (max-width: 768px) {
          transform: scale(0.8);
        }
        @media screen and (min-width: 320px) and (max-width: 500px) {
          transform: scale(0.5);
          right: -104px;
          top: -112px;
        }
      }

      .man {
        position: absolute;
        left: -105px;
        bottom: 0;
        @media screen and (min-width: 500px) and (max-width: 768px) {
          transform: scale(0.8);
          bottom: -48px;
        }
        @media screen and (min-width: 320px) and (max-width: 500px) {
          transform: scale(0.5);
          left: -119px;
          bottom: -122px;
        }
      }
    }
  }
}

.interpret {
  position: relative;
  padding-bottom: 136px;
  background: #f7ca59;

  @media (max-width: 767px) {
    padding-bottom: 100px;
  }
  h2 {
    font: normal 58px Playfair Display;
    line-height: 72px;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    padding-top: 130px;
    width: 90%;
    margin: 0 auto 150px;
    @media screen and (max-width: 991px) {
      margin: 0 auto;
      font-size: 30.8px;
      line-height: 40px;
      padding-top: 60px;
    }
    @media screen and (max-width: 424px) {
      font-size: 26.4px;
      line-height: 30px;
      width: 100%;
      margin: 0 auto;
      padding-top: 70px;
    }
  }

  .implement_body {
    position: relative;
    margin-top: 64px;
    @media screen and (min-width: 320px) and (max-width: 992px) {
      margin-top: 0px;
    }
    .caseMenu {
      margin: 30px auto 0;
      @media screen and (min-width: 991px) and (max-width: 1120px) {
        padding-left: 0;
      }
      @media screen and (min-width: 500px) and (max-width: 991px) {
        margin: 30px auto 0;
        padding-left: 50px;
      }
      @media screen and (min-width: 320px) and (max-width: 500px) {
        margin: 30px auto 0px;
        padding-left: 0px;
      }
      .caseMenu_item {
        position: relative;
        width: 352px;
        height: 91px;
        list-style: none;
        border-bottom: 2px solid #d0a73e;
        @media screen and (min-width: 320px) and (max-width: 767px) {
          width: 80%;
          height: 69px;
          margin: 0 auto;
        }

        &:last-child {
          margin-bottom: 0;
          border-bottom: none;
        }

        .linkAnimate {
          font: 900 20px niveau-grotesk;
          line-height: 28px;

          letter-spacing: 0.3px;
          text-transform: uppercase;
          color: $grey1;
          text-decoration: none;

          @media (max-width: 767px) {
            font-size: 14.04px;
            line-height: 20px;
          }
        }
      }

      .active {
        border-bottom: 2px solid #fc6839;
      }
    }

    .modernarousel.modernarousel-audits {
      width: 520px;
      height: 100%;
      background: #f6f0e1;
      border-radius: 10px;
      transform: translateX(-50px);

      @media screen and (min-width: 500px) and (max-width: 991px) {
        width: 80%;
        height: 90%;
        margin: 105px auto 0;
        transform: translateX(0px);
      }

      @media screen and (min-width: 200px) and (max-width: 500px) {
        width: 100%;
        height: 90%;
        margin: 148px auto 0;
        transform: translateX(0px);
      }
      
      .modernarousel_item {
        margin-top: 200px;

        @media (max-width: 768px){
          margin-top: 35px;
        }

        .modernarousel_title {
          font: 900 30px niveau-grotesk;
          line-height: 48px;
          text-align: center;
          text-transform: uppercase;
          color: #000000;

          @media (max-width: 768px) {
            font-size: 28px;
            line-height: 30px;
          }
          @media (max-width: 767px) {
            font-size: 26.4px;
            line-height: 30px;
          }
          @media (max-width: 425px) {
            font-size: 20.4px;
            line-height: 30px;
          }
        }

        .modernarousel_description {
          font: 500 20px niveau-grotesk;
          line-height: 28px;
          letter-spacing: 0.3px;
          color: $grey1;
          padding: 0 64px 64px;
          padding-left: 0;
          padding-right: 0;
          @media screen and (max-width: 768px) {
            font-size: 14.04px;
            line-height: 20px;
            padding: 0 10px 10px;
          }
        }
      }
    }

    .image_poster {
      position: absolute;
      bottom: -31%;
      left: 50px;
      @media screen and (max-width: 992px) {
        display: none;
      }
    }
  }
}

.modernarousel-audits_wrapper {
  position: relative;
}

.modernarousel_schema {
  position: absolute;
  top: 0; 
  width: 406px;
  border: 2px solid $grey1;
  border-bottom: 4px solid $grey1;
  border-left: 4px solid $grey1;
  transform: translateY(-50px);
  margin: 0 auto;
  z-index: 2;
  @media (max-width: 767px) {
    width: 258px;
    top: 40px;
    margin-left: -125px;
    left: 50%;
  }
  p {
    position: absolute;
    font: 500 20px niveau-grotesk;
    line-height: 28px;
    text-align: center;
    letter-spacing: 0.3px;
    color: $grey1;
    @media (max-width: 767px) {
      font-size: 14.04px;
      line-height: 20px;
    }
  }

  .text_left {
    transform: rotate(-90deg);
    left: -80px;
    top: 110px;

    @media screen and (min-width: 374px) and (max-width: 767px) {
      left: -27%;
    }
    @media (max-width: 374px) {
      left: -21%;
    }
  }

  .text_down {
    left: 50%;
    transform: translateX(-50%);
    bottom: -55px;
  }

  .plus {
    position: absolute;
    top: 0;
    left: 0;
    pointer-events: none;

    @media (max-width: 767px) {
      top: 7px;
      width: 100%;
    }
  }

  .arrow {
    position: absolute;
  }

  .arrow_top {
    top: -54px;
    left: -11px;

    @media (max-width: 767px) {
      display: none;
    }
  }
  .arrow_down {
    transform: rotate(90deg);
    bottom: -29px;
    right: -27px;
    @media (max-width: 767px) {
      display: none;
    }
  }

  .modernarousel_schema_item {
    width: 200px;
    height: 128px;
    background: #ffffff;
    cursor: pointer;
    @media (max-width: 767px) {
      width: 126px;
      height: 88px;
    }
    h5 {
      text-align: center;
      font: 900 13px niveau-grotesk;
      line-height: 20px;
      letter-spacing: 0.3px;
      text-transform: uppercase;
      color: $grey1;
    }
  }

  .modernarousel_item__active {
    background: #fc6839;

    h5 {
      color: #fff;
    }
  }
}


.questions {
  background-color: #f3dba2;
  h3 {
    padding-top: 161px;
    margin: 0 0 72px;

    @media screen and (min-width: 320px) and (max-width: 768px) {
      padding-top: 80px;
      margin: 0 0 10px;
      text-align: left;
    }
  }

  .card_body {
    width: 100%;
    padding-bottom: 130px;
    @media screen and (max-width: 768px) and(max-width: 991px) {
      padding-bottom: 100px;
    }
    @media screen and (max-width: 768px) {
      flex-direction: column;
      padding-bottom: 80px;
    }
    .card {
      width: 180px;
      height: 170px;
      background: $yellow;
      border-radius: 10px;

      @media screen and (min-width: 768px) and (max-width: 1024px) {
        width: 140px;
        height: 130px;
      }
      @media screen and (min-width: 320px) and (max-width: 768px) {
        margin: 0 auto;
        width: 254px;
        height: 85px;
      }

      h5 {
        font: 900 13px niveau-grotesk;
        line-height: 20px;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: $grey1;
        margin-left: 23px;

        @media screen and (min-width: 700px) and (max-width: 1024px) {
          margin-left: 15px;
        }

        br {
          @media screen and (min-width: 320px) and (max-width: 768px) {
