.policy{
    background: #F6F0E1;
    padding: 55px 0;

    h1 {
        font-size: 2.9em;
        font-weight: 400;
        line-height: 1.25em;
        margin-bottom: 35px;
    }

    h2{
        font-size: 2em;
        margin-bottom: 20px;
        margin-top: 55px;
    }

    p{
        margin-bottom: 25px;
    }

    a{
        color: #F7CA59;
        font-weight: 900;
    }

    ul{
        list-style: none;
        margin-bottom: 20px;
        padding-left: 0;

        li {
            position: relative;
            padding-left: 25px;
            font-size: 1.2em;
            margin-bottom: 10px;

            &::before{
                content: "/";
                position: absolute;
                left: 0;
                color: #F7CA59;
                font-weight: 900;
            }
        }
    }
}


@media (max-width: 770px){
    .policy p {
        font-size: 1.3em;
        line-height: 1.6em;
    }

    .policy ul li{
        font-size: 1.4em;
    }

}

@media (max-width: 540px){
    .policy{
        padding: 30px 0;
    }

    .policy h1 {
        margin-bottom: 20px;
        font-size: 2.2em;
    }

    .policy h2 {
        font-size: 1.9em;
        margin-top: 10px;
    }

}
