.singleArticle{
    padding-top: 75px;
    padding-bottom: 65px;
    background: #F6F0E1;
}

.body-content{
    max-width: 765px;
    margin-left: auto;
    margin-right: auto;
}

.article{
    &_header {
        margin-top: 46px;

        h1 {
            margin-top: 24px;
            margin-bottom: 30px;
            border-top: 6px solid #333333;
            border-bottom: 6px solid #333333;
            font-size: 2.5em;
            padding: 24px 0px;
            text-transform: uppercase;
            font-weight: 900;
        }
    }
    
    &_category{
        font-weight: 900;
        text-transform: uppercase;
    }
}

.articleImage{
    text-align: center;    

    &_img {
        display: inline-block;
        width: 100%;
        height: auto;
        position: relative;
        padding-left: 20px;
        padding-bottom: 20px;

    &::after {
        position: absolute;
        width: 96%;
        content: "";
        top: 30px;
        border-radius: 50px 10px 10px 10px;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 1;
        background: #FC6839;
    }


    img {
        width: 100%;
        position: relative;
        z-index: 2; 
        width: auto;
        border-radius: 10px 10px 50px 25px;
    }
}    
}

.articleImage_img img{
    width: 100%;
    height: auto;
}

.wp-block-separator {
    display: block;
    border: none;
    height: 4px;
    margin-top: 32px;
    background: #333333;
}

.body-content{
    h2{
        margin-top: 32px;
        margin-bottom: 25px;
        font-size: 2.3em;
    }

    h3{
        font-size: 1.6em;
        color: #E09178;
        margin-top: 38px;
        margin-bottom: 28px;
    }

    p {
        font-weight: 500;
    }

    strong {
        font-weight: 900;
    }

    .wp-block-quote p{
       
        position: relative;
        display: block;
        margin:40px 0px;
        padding: 40px;
        background: #F3DBA2;
        border-radius: 25px;
        font-size: 1.2em;
        font-style: normal;
        font-weight: 500;

        &::before{
            content: ''; 
            position: absolute; 
            left: 6%; 
            bottom: -30px; 
            border: 47px solid transparent; border-left: 84px solid #F3DBA2; border-top: 10px solid #F3DBA2;
        }
    }

    a{
        color: #FC6839;

        &:hover{
            text-decoration: underline;
        }
    }

    .line {
        position: relative;
        padding-left: 30px;
        margin-top: 35px;

        &::after{
            position: absolute;
            content: "";
            width: 4px;
            top: 7px;
            bottom: 7px;
            left: 0;
        }

        &::before{
            position: absolute;
            content: "";
            width: 2px;
            top: 7px;
            bottom: 7px;
            left: 9px;
        }

        &-red{
            &::after,&::before{background: #E09178;}
        }

        &-green{
            &::after,&::before{background: #A6D7C9;}
        }
    }

    .line_bottom {
        position: relative;
        padding-bottom: 20px;

        &::after{
            content: "";
            position: absolute;
            bottom:4px;
            left: 0;
            right: 3px;
            height: 4px;
        }

        &::before{
            content: "";
            position: absolute;
            bottom: -3px;
            left: 0;
            right: 3px;
            height: 2px;
        }

        &-red{
            &::after,&::before{background: #E09178;}
        }

        &-green{
            &::after,&::before{background: #A6D7C9;}
        }
    }
}

p.has-drop-cap:not(:focus):first-letter { 
    font-size: 90px;
    line-height: 30px;
    font-family:"poynter-oldstyle-display";
    margin-right: 32px;
    margin-top: 32px;
    
}

.wp-block-column {
    p:last-child{
        margin-bottom: 0;
    }
}

.wp-block-columns{
    margin-top: 24px;
}

.wp-block-column:not(:first-child){
    position: relative;
    // margin-left: 32px;

    &::before {
        position: absolute;
        content: "";
        top: 8px;
        left: -20px;
        bottom: 8px;
        background: #DBD1C5;
        width: 3px;    
    }

    p:last-child{
        margin-bottom: 0;
    }
}

.wp-block-columns.border-none .wp-block-column:not(:first-child){
    &::before{
        display: none;
    }
}

.wp-block-image {
    &.size-large{
        position: relative;
        width: 1120px;
        max-width: none;
        left: -178px;
        margin: 50px 0px;
        img {
            width: 100%;
            height: auto;
        }
    }

    &.img-small { 
        position: relative;
        width: 100%;
        left: auto;
        margin: 0;
        padding-right: 5px;       
    }
}


// Last NEWS
.articleBottom {
    background: #F6F0E1;

    &_news {
        display: flex;
    }

    &_wrapper{
        padding-bottom: 40px;
        border-bottom: 3px solid #333333;
    }
}

.share {
    display: flex;
    border-top: 4px solid #333333;
    margin-top: 35px;
    justify-content: flex-end;
    align-items: center;
    padding: 19px 0;

    &_title{
        font-weight: 900;
        margin-right: 24px;
    }

    &_link{
        margin-right: 15px;
        width: 32px;
        height: 32px;
    }
    &_link:hover {
        rect{
            transition: all 0.3s;
            fill: #FC6839;
        }
    }

    &_link:last-child {
        margin-right: 0;
    }
}

.articleSub {
    padding-top: 90px;
    padding-bottom: 90px;
    background-color: #F6F0E1;

    &_form{
        margin-top: 125px;
    }


    &_input{
        display: block;
        position: relative;
        right: 4px;
        border: 2px solid #333333;
        border-radius: 10px;
        width: 100%;
        padding: 15px 24px;
        margin-bottom: 29px;
        outline: none;
    }

    &_input:nth-child(2){
        margin-bottom: 37px;
    }
}

.articleSub_form {
    form {
        display: none;
    }    

    .screen-reader-response{
        padding: 10px;
        margin-top: 10px;
        font-family: 'Brandon Grotesque';
        
    
        ul {
          display: none; 
        }

    }
}


// MEDIA
@media (max-width: 1220px){
    .wp-block-image.size-large{
        margin: 25px 0px;
        width: 100%;
        left: auto;
    }

    .wp-block-image.size-large.img-small{
        margin: 0;
        padding: 0;
        margin-bottom: 15px;
    }
    
}

@media (max-width: 770px){

    .body-content p {
        font-size: 16px;
        line-height: 1.6em;
    }

    .articleSub_form{
        margin-top: 20px;
    }

    .articleSub{
        padding-top: 35px;
        padding-bottom: 35px;
    }

    .articleSub_input{
        padding: 8px 10px;
    }

    .articleSub_input{
        margin-bottom: 15px;
    }

    .articleSub_input:nth-child(2){
        margin-bottom: 25px;
    }
}

@media (max-width: 690px){
    .singleArticle{
        padding: 25px 0;
        padding-bottom: 35px;
    }

    .body-content p br{
        display: none;
    }

    .articleImage_img{       
        img{
            width: 100%;
            height: auto;
        }
    }

    .article_header{
        margin-top: 20px;
    }

    .article_header h1{
        padding: 12px 0;
        font-size: 2em;
        margin: 15px 0;
    }

    .body-content h2{
        margin:20px 0px;
        font-size: 1.8em;
    }

    .body-content h3{
        margin: 20px 0;
    }

    .body-content .line{
        margin-top: 20px;
    }

    .body-content .wp-block-quote p{
        padding: 20px;
        margin: 20px 0;
    }

    .body-content .wp-block-quote p::before {
        left: 6%;
        bottom: -19px;
        border: 28px solid transparent;
        border-left: 40px solid #F3DBA2;
        border-top: 10px solid #F3DBA2;
    }

    p.has-drop-cap:not(:focus):first-letter{
        font-size: 40px;
        margin-right: 20px;
        margin-top: 10px;
    }

    .articleBottom_wrapper {
        padding-bottom: 15px;
    }
}

@media(max-width: 600px){
    .wp-block-column:not(:first-child)::before{
        display: none;
    }

    .share {
        padding: 8px;
    }
}










