
@mixin button {
    position: relative;
    z-index: 2;
    display: inline-block;
   color: $base-font-color;
   outline: none!important;
   text-transform: uppercase;
   font-weight: 900;
   top: -7px;
   left: -6px;
   padding: 10px 32px;
   background: #fff;

   font-family: 'Brandon Grotesque';
   font-size: 14px;
   line-height: 20px;
   border: 4px solid $base-font-color;
   border-radius: 10px;
   -webkit-box-shadow: 7px 6px 0px -1px $base-font-color;;
    -moz-box-shadow: 7px 6px 0px -1px $base-font-color;
    box-shadow: 7px 6px 0px -1px $base-font-color;
    transition: all 0.3s;
    cursor: pointer;
    
    



    &:hover{
        box-shadow: 0px 0px 0px -1px rgba(51,51,51,1);
        transition: all 0.3s;
        color: $base-font-color;
        text-decoration: none;
        top: 0;
        left: 0;
    }
}

@mixin btnEnter{
    border: 4px solid $base-font-color;
    background-color: #fff;
    border-radius: 10px;
    display: flex;
    padding-left: 13px;
    padding-top: 9px;
    padding-bottom: 9px;
    padding-right: 27px;
    -webkit-box-shadow: 2px 2px 0px 1px $base-font-color;
    -moz-box-shadow: 2px 2px 0px 1px $base-font-color;
    box-shadow: 2px 2px 0px 1px $base-font-color;
    transition: all 0.3s;

} 


@mixin servGradient {
    background: rgba(166,215,201,1);
    background: -moz-linear-gradient(top, rgba(166,215,201,1) 0%, rgba(166,215,201,1) 88%, rgba(246,41,12,1) 88%, rgba(240,47,23,1) 88%, rgba(255,255,255,1) 88%, rgba(255,255,255,1) 100%);
    background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(166,215,201,1)), color-stop(88%, rgba(166,215,201,1)), color-stop(88%, rgba(246,41,12,1)), color-stop(88%, rgba(240,47,23,1)), color-stop(88%, rgba(255,255,255,1)), color-stop(100%, rgba(255,255,255,1)));
    background: -webkit-linear-gradient(top, rgba(166,215,201,1) 0%, rgba(166,215,201,1) 88%, rgba(246,41,12,1) 88%, rgba(240,47,23,1) 88%, rgba(255,255,255,1) 88%, rgba(255,255,255,1) 100%);
    background: -o-linear-gradient(top, rgba(166,215,201,1) 0%, rgba(166,215,201,1) 88%, rgba(246,41,12,1) 88%, rgba(240,47,23,1) 88%, rgba(255,255,255,1) 88%, rgba(255,255,255,1) 100%);
    background: -ms-linear-gradient(top, rgba(166,215,201,1) 0%, rgba(166,215,201,1) 88%, rgba(246,41,12,1) 88%, rgba(240,47,23,1) 88%, rgba(255,255,255,1) 88%, rgba(255,255,255,1) 100%);
    background: linear-gradient(to bottom, rgba(166,215,201,1) 0%, rgba(166,215,201,1) 88%, rgba(246,41,12,1) 88%, rgba(240,47,23,1) 88%, rgba(255,255,255,1) 88%, rgba(255,255,255,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6d7c9', endColorstr='#ffffff', GradientType=0 );

}

