  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(166,215,201,1)), color-stop(88%, rgba(166,215,201,1)), color-stop(88%, rgba(246,240,225,1)), color-stop(100%, rgba(246,240,225,1)));
  background: -webkit-linear-gradient(top, rgba(166,215,201,1) 0%, rgba(166,215,201,1) 88%, rgba(246,240,225,1) 88%, rgba(246,240,225,1) 100%);
  background: -o-linear-gradient(top, rgba(166,215,201,1) 0%, rgba(166,215,201,1) 88%, rgba(246,240,225,1) 88%, rgba(246,240,225,1) 100%);
  background: -ms-linear-gradient(top, rgba(166,215,201,1) 0%, rgba(166,215,201,1) 88%, rgba(246,240,225,1) 88%, rgba(246,240,225,1) 100%);
  background: linear-gradient(to bottom, rgba(166,215,201,1) 0%, rgba(166,215,201,1) 88%, rgba(246,240,225,1) 88%, rgba(246,240,225,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6d7c9', endColorstr='#f6f0e1', GradientType=0 );
}
