.termCon {
    padding-top: 130px;
    padding-bottom: 100px;
    background: #F6F0E1;
}

.conditions {
    margin-top: 110px;

    &_wrap{
        display: flex;
        align-items: center;
    }

    &_title{
        margin-left: 32px;
        text-transform: uppercase;
        margin-bottom: 0;
    }

    &_img{
        max-height: 70px;
    }

    &_desc{
        font-weight: 500;
        margin-top: 25px;
    }
}

.cl-vb{
    width: 55px;
}

.cl-java {
    width: 35px;
}

.cl-c {
    width: 100px;
}

.cl-basic {
    width: 100px;
}

.progLanguage {
    background: #F6F0E1;
    padding-top: 55px;
    padding-bottom: 100px;

    &_title{
        font-family: 'Brandon Grotesque';
        font-size: 1.7em;
        font-weight: 900;
        text-transform: uppercase;
    }

    &_desc{
        margin-top: 6px;
        font-weight: 500;
    }
}

.checkFor {
    background: #A6D7C9;
    padding: 105px 0;

    &_item{
        padding: 60px 70px;
        background: #F6F0E1;
        border-radius: 20px;
    }

    &_title {
        margin-left: 38px;
        font-size: 1.7em;
        font-weight: 900;
        line-height: 1.4em;
        text-transform: uppercase;
    }

    &_inner{
        display: flex;
        align-items: center;
    }
}

.checkForMenu {
    position: relative;
    margin-top: 22px;
    padding-left: 20px;
    list-style: none;

    &_item{
        position: relative;
        display: flex;
        align-items: center;
        font-weight: 500;
        margin-top: 32px;

        img {
            width: 20px;
            margin-right: 15px;
        }

        &::after {
            position: absolute;
            content: "";
            width: 12px;
            height: 3px;
            left: -18px;
            top: 32%;
            margin-top: 3px;
            background-color: #000;
        }

        &:first-child::after {
            display: none;
        }

        &.none::after {
            display: none;
        }
    }

    &_item:first-child{
        margin-top: 0;
    }

    &_line{
        width: 15px;
        top: 13px;
        bottom: 5%;
    }
}

.checkFor_item-pdf {
    background: #A6D7C9;

    .checkFor_item_line {
        top: 26px;
        bottom: 9%;
    }

}

.checkFor_line-pdf {
    top: 26px;
    bottom: 9%;
}

.liability {
    padding: 105px 0px;
    background: #FFF;

    &_title {
        font-family: 'Brandon Grotesque';
        font-weight: 900;
        text-transform: uppercase;
        font-size: 1.7em;
    }

}

.par-line {
    position: relative;
    padding-left: 30px;
    font-weight: 500;
    margin-top: 35px;

    &-red::before, &-red::after {
        background-color: #E09178;
    }

    &-green::before, &-green::after {
        background-color: #A6D7C9;
    }
}

.par-line::before{
    position: absolute;
    content: "";
    top: 7px;
    bottom: 7px;
    width: 2px;
    left: 9px; 
}

.par-line::after{
    width: 4px;
    left: 0;
    position: absolute;
    content: "";
    top: 7px;
    bottom: 7px;
}

.line-client-mt {
    margin-top: 20px;
    margin-bottom: 20px;
}

.page-contact .question-free {
    padding-bottom: 150px;
    padding-top: 120px;
}

.img-programmer {
    position: absolute;
    right: 0;
    bottom: -4%;
}

.img-squre {
    position: absolute;
    left: 0;
    top: 43%;
}

.img-girl {
    width: 180px;
    position: absolute;
    top: 0;
    left: 0;
}

.img-deg1 {
    position: absolute;
    right: 0;
    top:1%;
}

// POPUP
.overlay {
    display: none;
    position: fixed;
    z-index: 99;
    top: 0;
    left: 0;
    background-color: rgba(37,37,37, 0.7);
    height: 100vh;
    width: 100%;
}

.popup {
    position: absolute;
    width: 736px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.quize-popup {
    width: 736px;
    margin-top: 0;
    height: auto;
    padding-bottom: 15px;
}

.quize_body-popup{
    margin-top: 55px;
    padding: 0 15px;
    margin-right: 0;
}

.popup_form.contactForm {
    width: 100%;
}

.popup_form{
    margin: 0 auto;
    max-width: 400px;

    .inputSubmit{
        margin-top: 30px;
    }
}

.quize_popup_close {
    img{
        width: 24px;
        height: 24px;
        cursor: pointer;
    }
}

.popup_desc {
    text-align: center;
    font-weight: 500;
    margin-bottom: 35px;
}

.popup_title {
    text-transform: uppercase;
    font-size: 1.4em;
    line-height: 1.4em;
    font-weight: 900;
    margin-bottom: 35px;
    text-align: center;
}



// Form
@media (max-width: 1170px){
    .img-programmer{
        width: 290px;
    }

}

@media (max-width: 990px){
    .img-squre {
        display: none;
    }

    .h2_desc-code br {
        display: none;
    }

    .quize-popup {
        height: auto!important;
    }

    .quize-popup {
        max-width: 600px;
        width: 90%;
    }

    .img-girl{
        width: 120px;
        bottom: -4%;
        top: auto;
    }

}


@media (max-width: 770px){
    .termCon{
        padding-top: 50px;
        padding-bottom: 50px;
    }

    .conditions {
        margin-top: 20px;
    }

    .legacy-mt {
        margin-top: 120px;
    }

    .progLanguage{
        padding: 50px 0px;
        padding-top: 0;
    }

    .checkForMenu_line {
        display: none;
    }

    .checkForMenu_item::after {
        display: none;
    }

    .checkFor {
        padding: 55px 0px;
    }

    .checkFor_item {
        padding: 15px;
    }

    .checkFor_title {
        br {
            display: none;
        }
    }

    .checkForMenu_item {
        font-size: 1.2em;
        margin-top: 15px;
    }

    .conditions_desc {
        font-size: 14px;
    }

    .checkFor_img {
        max-width: 50px;
    }

    .liability {
        padding: 55px 0px;
    }

    .line-client-mt {
        margin-top: 15px!important;
        margin-bottom: 15px!important;
    }

    .img-programmer {
        width: 220px;
    }

    .img-deg1 {
        width: 75px;
    }

    .quize_body-popup {
        margin-bottom: 15px;
    }

    .popup_title {
        margin-bottom: 15px;
    }
    .popup_desc {
        margin-bottom: 15px;
    }

    .liability .par-line {
        font-size: 14px;
        line-height: 1.4em;
    }
}

@media (max-width: 540px){
    .legacy-mt {
        margin-top: 50px!important;
    }
}