$grey1: #333333;
$pink: #f6f0e1;
$white: #ffffff;
$yellow: #f7ca59;
$blue: #a6d7c9;
$bloomarin: #bae8db;

.am-welcome {
  position: relative;
  padding-top: 130px;
  background: $pink;
  padding-bottom: 108px;

  .block-wrap {
    justify-content: space-between;

    @media screen and (min-width: 320px) and (max-width: 1145px) {
      justify-content: center;
    }
  }

  .am-welcome-service {
    overflow: hidden;
    padding-top: 30px;
  }

  h1 {
    font: 60px Playfair Display;
    line-height: 120%;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    margin: 0;

    @media (max-width: 768px) {
      font-size: 26.4px;
    }
  }

  p {
    font: normal 20px niveau-grotesk;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.3px;
    margin-top: 40px;
    color: $grey1;

    @media screen and (max-width: 768px) {
      font-size: 12px;
      margin: 20px auto 0;
    }
  }

  .block-wrap {
    position: relative;
    height: auto;
    margin: 33px auto 0;
    z-index: 0;

    .block-content {
      .block-about {
        margin-left: 52px;
        width: 1055px;
        height: 510px;
        background: $white;
        border: 4px solid $grey1;
        box-sizing: border-box;
        border-radius: 20px;
        z-index: 1;

        @media screen and (min-width: 992px) and (max-width: 1145px) {
          width: 1000px;
        }

        @media screen and (min-width: 550px) and (max-width: 1200px) {
          height: auto;
          width: 80%;
          margin: 0 auto;
        }

        @media screen and (min-width: 320px) and (max-width: 550px) {
          height: auto;
          width: 99%;
          margin: 0 auto;
        }

        .block-about_body {
          margin: 0 32px;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            margin: 0 2px;
          }

          .am-schema_inner {
            position: relative;
            padding-top: 60px;
            @media screen and (min-width: 990px) and (max-width: 1200px) {
              padding: 73px 73px 0;
              height: 505px;
            }
            @media screen and (min-width: 768px) and (max-width: 990px) {
              padding: 40px 20px 0;
              height: 479px;
            }
            @media screen and (min-width: 500px) and (max-width: 768px) {
              padding: 40px 20px 0;
              height: 385px;
              margin-bottom: -45px;
            }
            @media screen and (min-width: 320px) and (max-width: 500px) {
              padding: 40px 0px 0;
              height: 320px;
              margin-bottom: -45px;
            }

            .blockBorder {
              position: absolute;
              top: 33%;
              z-index: 1;
              width: 336px;
              height: 217px;
              border: 4px solid #fcbd3b;
              @media screen and (min-width: 320px) and (max-width: 768px) {
                top: 30%;
                height: 172px;
              }

              @media screen and (min-width: 400px) and (max-width: 500px) {
                top: 36%;
                width: 262px;
                height: 135px;
              }

              @media screen and (min-width: 320px) and (max-width: 400px) {
                width: 190px;
                top: 36%;
                height: 135px;
              }
            }

            .am-schema_item {
              z-index: 2;
              width: 228px;
              height: 176px;
              background: $yellow;
              border-radius: 10px;
              box-shadow: $grey1 6px 6px;

              @media screen and (min-width: 500px) and (max-width: 768px) {
                width: 180px;
                height: 130px;
              }

              @media screen and (min-width: 400px) and (max-width: 500px) {
                width: 169px;
                height: 113px;
              }

              @media screen and (min-width: 320px) and (max-width: 400px) {
                width: 119px;
                height: 113px;
              }

              &:first-child {
                margin-bottom: 40px;
              }

              h5 {
                font: bold 14px niveau-grotesk;
                line-height: 20px;
                letter-spacing: 0.3px;
                text-transform: uppercase;
                text-align: center;
                margin-top: 28px;
                @media screen and (min-width: 320px) and (max-width: 768px) {
                  margin-top: 10px;
                }
              }

              img {
                @media screen and (min-width: 500px) and (max-width: 768px) {
                  transform: scale(0.7);
                }
                @media screen and (min-width: 320px) and (max-width: 500px) {
                  transform: scale(0.7);
                  margin-bottom: -10px;
                }
              }
            }
          }

          .list_inner {
            position: relative;
            padding-left: 70px;
            @media screen and (min-width: 400px) and (max-width: 1200px) {
              padding-left: 40px;
            }
            @media screen and (min-width: 320px) and (max-width: 400px) {
              padding-left: 4px;
            }
            h3 {
              padding-top: 36px;
              font: 900 34px niveau-grotesk;
              line-height: 48px;
              color: $grey1;

              @media (max-width: 768px) {
                font-size: 22.4px;
              }
            }
            .list {
              margin-top: 32px;
              padding-left: 25px;
              @media screen and (min-width: 320px) and (max-width: 550px) {
                margin-top: 0px;
              }
              .list_item {
                list-style: none;
                margin-bottom: 20px;
                @media screen and (min-width: 320px) and (max-width: 550px) {
                  margin-bottom: 5px;
                }
                position: relative;
                &:nth-child(2),
                &:nth-child(3) {
                  &:after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: -25px;
                    transform: translateY(-50%);
                    width: 20px;
                    height: 3px;
                    background-color: #000000;
                  }
                }
                .list_img {
                  margin-right: 13px;
                }
                .list_title {
                  font: normal 22px niveau-grotesk;
                  line-height: 32px;
                  letter-spacing: 0.3px;
                  color: $grey1;

                  @media (max-width: 768px) {
                    font-size: 14.04px;
                  }
                }
              }
            }
            .list_line {
              position: absolute;
              top: 146px;
              bottom: 11%;
              left: 16.5%;
              width: 20px;
              border-radius: 7px 0 0 7px;
              border-left: 3px solid #000;
              border-top: 3px solid #000;
              border-bottom: 3px solid #000;

              @media screen and (min-width: 901px) and (max-width: 1200px) {
                left: 4.7%;
                top: 131px;
                bottom: 11%;
              }
              @media screen and (min-width: 814px) and (max-width: 901px) {
                left: 39px;
                top: 131px;
                bottom: 11%;
              }
              @media screen and (min-width: 769px) and (max-width: 814px) {
                left: 39px;
                top: 145px;
                bottom: 11%;
              }
              @media screen and (min-width: 550px) and (max-width: 769px) {
                left: 39px;
                top: 130px;
                bottom: 9%;
              }
              @media screen and (min-width: 446px) and (max-width: 550px) {
                left: 39px;
                top: 104px;
                bottom: 13%;
              }
              @media screen and (min-width: 400px) and (max-width: 446px) {
                left: 39px;
                top: 123px;
                bottom: 9%;
              }
              @media screen and (min-width: 336px) and (max-width: 400px) {
                left: 3px;
                top: 122px;
                bottom: 11%;
              }
              @media screen and (min-width: 320px) and (max-width: 335px) {
                left: 3px;
                top: 122px;
                bottom: 13%;
              }
            }
          }
        }

        &:after {
          content: " ";
          position: absolute;
          right: 0;
          top: 16px;
          z-index: -1;
          width: 1031px;
          height: 510px;
          background: $grey1;
          border-radius: 20px;
          @media screen and (max-width: 1200px) {
            width: 831px;
            right: 14%;
            height: 526px;
          }
          @media screen and (max-width: 1199px) {
            width: 831px;
            right: 14%;
            height: 100%;
          }

          @media screen and (min-width: 550px) and (max-width: 1190px) {
            height: 100%;
            width: 77%;
            margin: 0 auto;
            right: 8%;
          }
          @media screen and (min-width: 320px) and (max-width: 550px) {
            height: 100%;
            width: 99%;
            margin: 0 auto;
            right: -2%;
          }
        }
      }
    }
    .block-marker {
      position: absolute;
      width: 55px;
      height: 244px;
      background: $grey1;
      border-radius: 20px 0px 0px 20px;
      left: 0;
      top: 0;
      z-index: -1;

      @media screen and (min-width: 768px) and (max-width: 1200px) {
        left: 5%;
      }
      @media screen and (min-width: 550px) and (max-width: 768px) {
        left: 3%;
      }
      @media screen and (min-width: 320px) and (max-width: 550px) {
        left: 15%;
      }
      p {
        color: $white;
        white-space: nowrap;
        font: 900 14px niveau-grotesk;
        line-height: 143%;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        margin: 0;
        transform: rotate(-90deg) translateY(-5px);
      }
    }
  }
}

.youNeedIt {
  background-color: $blue;
  padding-bottom: 59px;
  position: relative;

  @media screen and (min-width: 320px) and (max-width: 1200px) {
    padding-bottom: 100px;
  }

  .stairs {
    position: absolute;
    width: 100%;
    top: 3px;
    .stairs_item {
      display: block;
      background-color: $pink;
      margin-bottom: 6px;
    }
    .stairs_item-two {
      height: 2px;
    }
    .stairs_item-four {
      height: 4px;
    }
    .stairs_item-eight {
      height: 8px;
    }
    .stairs_item:last-child {
      margin-bottom: 0;
    }
  }

  h3 {
    font: 900 32px niveau-grotesk;
    text-transform: uppercase;
    color: $grey1;
    padding-top: 99px;

    @media screen and (max-width: 768px) {
      font-size: 20.4px;
    }
  }
  .youNeedIt_body {
    position: relative;
    .cards {
      margin-top: 40px;
      justify-content: space-between;

      @media screen and (min-width: 320px) and (max-width: 1200px) {
        justify-content: center;
      }

      .card_item {
        width: 430px;
        height: 128px;
        background: $bloomarin;
        border-radius: 10px;
        margin: 0 5px 33px;

        @media screen and (min-width: 768px) and (max-width: 910px) {
          height: 158px;
          width: 349px;
        }
        @media screen and (min-width: 594px) and (max-width: 768px) {
          height: 102px;
          width: 272px;
        }
        @media screen and (min-width: 423px) and (max-width: 594px) {
          height: 157px;
          width: 187px;
          margin: 0 5px 10px;
        }
        @media screen and (min-width: 320px) and (max-width: 423px) {
          height: 116px;
          width: 334px;
          margin: 0 5px 10px;
        }

        h4 {
          font: 900 20px niveau-grotesk;
          line-height: 28px;
          letter-spacing: 0.3px;
          text-transform: uppercase;
          color: $grey1;
          margin: 0 5px 0 24px;
          @media screen and (max-width: 768px) {
            font-size: 14.04px;
          }
          @media screen and (min-width: 320px) and (max-width: 594px) {
            margin: 0 0 0 10px;
            line-height: 20px;
          }
        }
      }
    }
    .image {
      position: absolute;
      right: -7%;
      bottom: -23px;
      width: 237px;
      height: 222px;
      background: #fcbd3b;
      border-radius: 10px;

      @media screen and (min-width: 1270px) and (max-width: 1440px) {
        right: -5%;
      }
      @media screen and (min-width: 1200px) and (max-width: 1270px) {
        right: -3%;
        transform: scale(0.9);
      }
      @media screen and (min-width: 768px) and (max-width: 1200px) {
        bottom: -51%;
        right: 2%;
        transform: scale(0.9);
      }
      @media screen and (min-width: 423px) and (max-width: 768px) {
        bottom: -60%;
        right: 2%;
        transform: scale(0.8);
      }

      @media screen and (min-width: 320px) and (max-width: 423px) {
        bottom: -38%;
        right: -2%;
        transform: scale(0.7);
      }

      img {
        position: absolute;
        right: 14px;
        top: 12px;
        border-radius: 10px;
        border-bottom-left-radius: 50px;
      }
    }
  }
}

.ai-advice {
  .ai-advice_body {
    padding: 146px 0 64px;
    .ai-advice_card {
      position: relative;
      width: 896px;
      height: 466px;
      background: #f6f0e1;
      border-radius: 20px;

      @media screen and (min-width: 768px) and (max-width: 980px) {
        width: 790px;
      }
      @media screen and (min-width: 320px) and (max-width: 768px) {
        width: 100%;
        height: auto;
      }

      .ai-advice_block__left {
        @media screen and (min-width: 768px) and (max-width: 980px) {
          transform: translateX(17px);
        }
        @media screen and (min-width: 320px) and (max-width: 768px) {
          order: 1;
        }
      }

      .ai-advice_block__right {
        @media screen and (min-width: 320px) and (max-width: 768px) {
          order: 0;
        }
      }

      .ai-adviceQuotes_img {
        position: absolute;
        left: -30px;
        top: -27px;
        @media screen and (min-width: 768px) and (max-width: 980px) {
          left: 6px;
        }
        @media screen and (min-width: 320px) and (max-width: 768px) {
          left: 6px;
          transform: scale(0.8);
        }
      }
      .ai-advice_img {
        transform: translateY(-32px);

        @media screen and (min-width: 400px) and (max-width: 768px) {
          transform: translateY(0);
          margin: 20px auto 50px;
          order: 1;
        }
        @media screen and (min-width: 320px) and (max-width: 400px) {
          transform: translateY(0);
          margin: 20px auto 50px;
          order: 1;
          width: 95%;
        }
      }
      .ai-advice_text {
        margin: -16px 0 0 64px;
        @media screen and (min-width: 320px) and (max-width: 768px) {
          margin: 0px;
        }
        h4 {
          font: bold 20px niveau-grotesk;
          line-height: 30px;
          letter-spacing: 0.3px;
          color: $grey1;
          margin: 0;
          @media screen and (min-width: 400px) and (max-width: 768px) {
            font-size: 16px;
            width: 70%;
            margin: 15px auto 0;
          }
          @media screen and (min-width: 320px) and (max-width: 400px) {
            font-size: 16px;
            width: 85%;
            margin: 12px auto 0;
          }
        }
        p {
          font: normal 16px niveau-grotesk;
          line-height: 24px;
          letter-spacing: 0.3px;
          color: $grey1;
          opacity: 0.8;
          @media screen and (min-width: 320px) and (max-width: 768px) {
            font-size: 14.04px;
          }
        }
      }
      h2 {
        position: relative;
        font: 900 50px niveau-grotesk;
        width: 80%;
        line-height: 64px;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: $grey1;
        margin: 87px 0 59px;
        @media screen and (min-width: 768px) and (max-width: 980px) {
          margin: 87px 0 59px 10px;
        }
        @media screen and (min-width: 500px) and (max-width: 768px) {
          margin: 50px auto 30px;
          font-size: 22.4px;
          text-align: center;
        }
        @media screen and (min-width: 320px) and (max-width: 500px) {
          margin: 50px auto 30px;
          font-size: 22.4px;
          text-align: center;
          width: 85%;
        }
        &::before {
          content: "";
          position: absolute;
          width: 384px;
          height: 6px;
          left: 0;
          top: -17px;
          background: $grey1;
          @media screen and (min-width: 768px) and (max-width: 980px) {
            width: 325px;
          }

          @media screen and (min-width: 400px) and (max-width: 768px) {
            left: 50%;
            transform: translate(-50%);
            height: 4px;
            top: -10px;
            width: 325px;
          }
          @media screen and (min-width: 320px) and (max-width: 400px) {
            left: 50%;
            transform: translate(-50%);
            height: 4px;
            top: -10px;
            width: 275px;
          }
        }
        &::after {
          content: "";
          position: absolute;
          width: 384px;
          height: 6px;
          left: 0;
          bottom: -17px;
          background: $grey1;
          @media screen and (min-width: 320px) and (max-width: 980px) {
            width: 325px;
          }
          @media screen and (min-width: 400px) and (max-width: 768px) {
            left: 50%;
            transform: translate(-50%);
            height: 4px;
            bottom: -10px;
            width: 325px;
          }
          @media screen and (min-width: 320px) and (max-width: 400px) {
            left: 50%;
            transform: translate(-50%);
            height: 4px;
            bottom: -10px;
            width: 275px;
          }
        }
      }

      p {
        font: normal 24px niveau-grotesk;
        line-height: 32px;
        letter-spacing: 0.3px;
        color: $grey1;
        width: 90%;
        @media screen and (min-width: 400px) and (max-width: 768px) {
          font-size: 14.04px;
          margin: 0 auto;
          width: 70%;
          line-height: 20px;
        }
        @media screen and (min-width: 320px) and (max-width: 400px) {
          font-size: 14.04px;
          margin: 0 auto;
          width: 85%;
          line-height: 20px;
        }
      }
    }
  }
}

.ai-principles {
  background: $blue;
  padding-bottom: 160px;
  @media (max-width: 768px) {
    padding-bottom: 60px;
  }
  h2 {
    font: normal 58px Playfair Display;
    line-height: 72px;
    text-align: center;
