    @media (max-width: 768px) {
      padding-top: 50px;
      font-size: 1.4em;
      line-height: 32px;
      margin: 0 auto 30px;
    }
  }

  .ai-principles_item {
    margin-bottom: 32px;
    p {
      width: 71%;
      text-align: center;
      font: normal 20px niveau-grotesk;
      line-height: 30px;
      letter-spacing: 0.3px;
      color: $grey1;
      margin-top: 20px;

      @media (max-width: 768px) {
        font-size: 14.04px;
        line-height: 20px;
      }
    }
    .ai-principles_image {
      width: 61px;
      height: 68px;
      margin: 0 auto;
      @media (max-width: 768px) {
