      }
    }
  }
}

.process {
  position: relative;
  padding-bottom: 136px;
  .stairs {
    position: absolute;
    width: 100%;
    top: 3px;
    .stairs_item {
      display: block;
      background-color: $blue;
      margin-bottom: 6px;
    }
    .stairs_item-two {
      height: 2px;
    }
    .stairs_item-four {
      height: 4px;
    }
    .stairs_item-eight {
      height: 8px;
    }
    .stairs_item:last-child {
      margin-bottom: 0;
    }
  }
  h2 {
    font: normal 58px Playfair Display;
    line-height: 72px;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    padding-top: 130px;
    width: 90%;
    margin: 0 auto 64px;

    @media (max-width: 768px) {
      font-size: 22.4px;
      line-height: 30px;
      width: 100%;
      margin: 0 auto 40px;
      padding-top: 70px;
    }
  }

  .caseMenu {
    margin-bottom: 0;
    @media (max-width: 1120px) {
      padding-left: 0;
    }
    .caseMenu_item {
      position: relative;
      width: 443px;
      height: 91px;
      background: $pink;
      border-radius: 10px;
      margin-bottom: 22px;
      list-style: none;
      @media screen and (min-width: 768px) and (max-width: 992px) {
        width: 107%;
      }
      @media screen and (min-width: 576px) and (max-width: 768px) {
        width: 100%;
        height: 70px;
      }
      @media screen and (min-width: 320px) and (max-width: 576px) {
        width: 90%;
        height: 65px;
        min-width: 250px;
        margin: 0 auto 10px;
      }

      &:last-child {
        margin-bottom: 0;
      }

      .caseMenu_code {
        width: 58px;
        height: 32px;
        background: #ffffff;
        border-radius: 36px;
        font: normal 20px Fira Code;
        line-height: 32px;
        text-align: center;
        color: #888888;
        margin: 0 16px 0 24px;
        @media (max-width: 992px) {
          margin: 0 16px 0 10px;
        }
        @media (max-width: 768px) {
          font-size: 14.04px;
          line-height: 20px;

          width: 44px;
          height: 22px;
          margin: 0 16px 0 10px;
        }
      }

      .linkAnimate {
        font: bold 20px niveau-grotesk;
        line-height: 30px;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: #333333;
        text-decoration: none;

        @media (max-width: 768px) {
          font-size: 14.04px;
          line-height: 20px;
        }
      }

      .caseMenu_arrow {
        position: absolute;
        right: 17px;

        @media (max-width: 768px) {
          display: none;
        }
      }
    }

    .active {
      background: #fc6839;
      box-shadow: #333333 7px 7px;

      .linkAnimate {
        color: #ffffff;
      }

      .caseMenu_arrow {
        display: none;
      }
    }
  }

  .am-modernarousel {
    width: 640px;
    height: 100%;
    transform: translateX(-8%);
    background-color: $pink;
    border-radius: 10px;

    @media screen and (min-width: 992px) and (max-width: 1220px) {
      width: 100%;
      height: calc(100% - 40px);
    }

    @media screen and (min-width: 576px) and (max-width: 992px) {
      width: 100%;
      height: 100%;
      transform: translateX(2%);
    }

    @media screen and (min-width: 320px) and (max-width: 576px) {
      width: 95%;
      height: 100%;
      margin: 25px auto;
      transform: translateX(0%);
