$grey1: #333333;
$pink: #f6f0e1;
$white: #ffffff;
$yellow: #f7ca59;
$blue: #a6d7c9;
$bloomarin: #bae8db;

body {
  margin: 0;
}

.container-wrap {
  max-width: 1150px;
  margin: 0 auto;
}

.section_title {
  font: normal 58px Playfair Display;
  line-height: 72px;
  margin-top: 130px;
  text-align: center;
  text-transform: capitalize;
  color: $grey1;

  @media (max-width: 768px) {
    font-size: 1.4em;
  }
}

// first section
.am-welcome {
  position: relative;
  padding-top: 130px;
  background: $pink;
  padding-bottom: 108px;

  .block-wrap {
    justify-content: space-between;

    @media screen and (min-width: 320px) and (max-width: 1145px) {
      justify-content: center;
    }
  }

  .am-welcome-service {
    overflow: hidden;
    padding-top: 30px;
  }

  h1 {
    font: 60px Playfair Display;
    line-height: 120%;
    text-align: center;
    color: $grey1;
    margin: 0;

    @media (max-width: 768px) {
      font-size: 26.4px;
    }
  }

  p {
    font: normal 20px niveau-grotesk;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.3px;
    margin-top: 40px;
    color: $grey1;

    @media screen and (max-width: 768px) {
      font-size: 12px;
      margin: 20px auto 0;
    }
  }

  .block-wrap {
    position: relative;
    height: auto;
    margin: 33px auto 0;
    z-index: 0;

    .block-content {
      .block-about {
        margin-left: 52px;
        width: 1055px;
        height: 510px;
        background: $white;
        border: 4px solid $grey1;
        box-sizing: border-box;
        border-radius: 20px;
        z-index: 1;

        @media screen and (min-width: 992px) and (max-width: 1145px) {
          width: 1000px;
        }

        @media screen and (min-width: 550px) and (max-width: 1200px) {
          height: auto;
          width: 80%;
          margin: 0 auto;
        }

        @media screen and (min-width: 320px) and (max-width: 550px) {
          height: auto;
          width: 99%;
          margin: 0 auto;
        }

        .block-about_body {
          margin: 0 32px;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            margin: 0 2px;
          }

          .am-schema_inner {
            padding-top: 60px;

            @media screen and (min-width: 320px) and (max-width: 400px) {
              padding: 40px 2px 0;
            }

            .am-schema_screens {
              justify-content: space-evenly;
              position: relative;
              &::after {
                content: "";
                position: absolute;
                width: 4px;
                height: 260px;
                top: -60px;
                background-color: $grey1;
                @media screen and (min-width: 320px) and (max-width: 400px) {
                  height: 182px;
                  top: -65px;
                }
              }

              .am-schema_body {
                .am-schema_title {
                  font: 900 16px niveau-grotesk;
                  line-height: 24px;
                  display: flex;
                  align-items: center;
                  text-align: center;
                  letter-spacing: 0.3px;
                  color: $grey1;
                  margin-top: 19px;
                  @media screen and (max-width: 768px) {
                    font-size: 14.04px;
                    margin-top: 5px;
                  }
                }
              }

              .am-arrow {
                padding-top: 6px;
                z-index: 2;

                @media screen and (min-width: 320px) and (max-width: 768px) {
                  display: flex;
                  justify-content: center;
                  align-items: baseline;
                  width: 50%;
                }
              }

              svg, img {
                @media screen and (min-width: 320px) and (max-width: 768px) {
                  width: 50%;
                  height: auto;
                }
              }
            }

            .am-schema_cards {
              position: relative;
              z-index: 2;
              margin-top: 35px;
              justify-content: space-between;
              @media screen and (min-width: 768px) and (max-width: 1200px) {
                justify-content: space-around;
              }
              @media screen and (min-width: 520px) and (max-width: 768px) {
                justify-content: space-between;
              }
              @media screen and (min-width: 320px) and (max-width: 520px) {
                margin-top: 19px;
                justify-content: space-between;
              }

              .am-schema_card {
                position: relative;
                width: 144px;
                height: 176px;
                background: $yellow;
                border-radius: 10px;

                @media screen and (min-width: 768px) and (max-width: 1200px) {
                  width: 130px;
                  height: 163px;
                }

                @media screen and (min-width: 550px) and (max-width: 768px) {
                  width: 100px;
                  height: 105px;
                }

                @media screen and (min-width: 320px) and (max-width: 550px) {
                  width: 91px;
                  padding-top: 5px;
                  padding-bottom: 5px;
                  height: auto;
                }

                &:nth-child(2),
                &:nth-child(3) {
                  &:before {
                    content: "";
                    position: absolute;
                    width: 50px;
                    height: 4px;
                    left: -48px;
                    background-color: #fcbd3b;
                    z-index: -2;

                    @media screen and (min-width: 550px) and (max-width: 1200px) {
                      width: 124px;
                      height: 4px;
                      left: -119px;
                    }

                    @media screen and (min-width: 400px) and (max-width: 550px) {
                      width: 110px;
                      height: 4px;
                      left: -110px;
                    }

                    @media screen and (min-width: 320px) and (max-width: 400px) {
                      width: 60px;
                      height: 4px;
                      left: -60px;
                    }
                  }
                }

                &:after {
                  content: " ";
                  position: absolute;
                  width: 144px;
                  height: 176px;
                  left: 8px;
                  top: 9px;
                  z-index: -1;
                  background: $grey1;
                  border-radius: 10px;

                  @media screen and (min-width: 768px) and (max-width: 1200px) {
                    width: 130px;
                    height: 163px;
                  }
                  @media screen and (min-width: 550px) and (max-width: 768px) {
                    width: 100px;
                    height: 105px;
                  }
                  @media screen and (min-width: 401px) and (max-width: 550px) {
                    width: 82px;
                    height: 81px;
                  }
                  @media screen and (min-width: 320px) and (max-width: 401px) {
                    width: 0;
                    height: 0;
                  }
                }

                .am-schema_title {
                  font: 900 13px niveau-grotesk;
                  line-height: 20px;
                  margin-top: 16px;
                  display: flex;
                  align-items: center;
                  text-align: center;
                  letter-spacing: 0.3px;
                  color: $grey1;
                  @media screen and (max-width: 768px) {
                    font-size: 12px;
                  }
                  @media screen and (min-width: 320px) and (max-width: 768px) {
                    margin-top: 10px;
                  }
                }
                .am-schema_wrap-img{
                  min-height: 41px;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                }
                svg, img {
                  @media screen and (min-width: 320px) and (max-width: 768px) {
                    width: 45%;
                    height: auto;
                  }
                }
              }
            }
          }

          .list_inner {
            position: relative;
            padding-left: 70px;
            @media screen and (min-width: 400px) and (max-width: 1200px) {
              padding-left: 40px;
            }
            @media screen and (min-width: 320px) and (max-width: 400px) {
              padding-left: 4px;
            }
            h3 {
              padding-top: 36px;
              font: 900 34px niveau-grotesk;
              line-height: 48px;
              color: $grey1;

              @media (max-width: 960px) {
                font-size: 22px;
              }
            }
            .list {
              margin-top: 32px;
              padding-left: 25px;
              @media screen and (min-width: 320px) and (max-width: 550px) {
                margin-top: 0px;
              }
              .list_item {
                list-style: none;
                margin-bottom: 20px;
                @media screen and (min-width: 320px) and (max-width: 550px) {
                  margin-bottom: 5px;
                }
                position: relative;
                &:nth-child(2),
                &:nth-child(3) {
                  &:after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: -25px;
                    transform: translateY(-50%);
                    width: 20px;
                    height: 3px;
                    background-color: #000000;
                  }
                }
                .list_img {
                  margin-right: 13px;
                }
                .list_title {
                  font: normal 22px niveau-grotesk;
                  line-height: 32px;
                  letter-spacing: 0.3px;
                  color: $grey1;

                  @media (max-width: 768px) {
                    font-size: 14.04px;
                  }
                }
              }
            }
            .list_line {
              position: absolute;
              top: 146px;
              bottom: 14%;
              left: 16.5%;
              width: 20px;
              border-radius: 7px 0 0 7px;
              border-left: 3px solid #000;
              border-top: 3px solid #000;
              border-bottom: 3px solid #000;

              @media screen and (min-width: 901px) and (max-width: 1200px) {
                left: 4.7%;
                top: 131px;
                bottom: 11%;
              }
              @media screen and (min-width: 814px) and (max-width: 901px) {
                left: 39px;
                top: 131px;
                bottom: 11%;
              }
              @media screen and (min-width: 769px) and (max-width: 814px) {
                left: 39px;
                top: 145px;
                bottom: 11%;
              }
              @media screen and (min-width: 550px) and (max-width: 769px) {
                left: 39px;
                top: 130px;
                bottom: 9%;
              }
              @media screen and (min-width: 446px) and (max-width: 550px) {
                left: 39px;
                top: 104px;
                bottom: 13%;
              }
              @media screen and (min-width: 400px) and (max-width: 446px) {
                left: 39px;
                top: 123px;
                bottom: 9%;
              }
              @media screen and (min-width: 336px) and (max-width: 400px) {
                left: 3px;
                top: 122px;
                bottom: 11%;
              }
              @media screen and (min-width: 320px) and (max-width: 335px) {
                left: 3px;
                top: 122px;
                bottom: 13%;
              }
            }
          }
        }

        &:after {
          content: " ";
          position: absolute;
          right: 0;
          top: 16px;
          z-index: -1;
          width: 1031px;
          height: 510px;
          background: $grey1;
          border-radius: 20px;
          @media screen and (max-width: 1200px) {
            width: 831px;
            right: 14%;
            height: 526px;
          }
          @media screen and (max-width: 1199px) {
            width: 831px;
            right: 14%;
            height: 100%;
          }

          @media screen and (min-width: 550px) and (max-width: 1190px) {
            height: 100%;
            width: 77%;
            margin: 0 auto;
            right: 8%;
          }
          @media screen and (min-width: 320px) and (max-width: 550px) {
            height: 100%;
            width: 99%;
            margin: 0 auto;
            right: -2%;
          }
        }
      }
    }
    .block-marker {
      position: absolute;
      width: 70px;
      height: 244px;
      background: $grey1;
      border-radius: 20px 0px 0px 20px;
      left: 0;
      top: 0;
      z-index: -1;

      @media screen and (min-width: 768px) and (max-width: 1200px) {
        left: 5%;
      }
      @media screen and (min-width: 550px) and (max-width: 768px) {
        left: 3%;
      }
      @media screen and (min-width: 320px) and (max-width: 550px) {
        left: 15%;
      }
      p {
        color: $white;
        white-space: nowrap;
        font: 900 14px niveau-grotesk;
        line-height: 143%;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        margin: 0;
        transform: rotate(-90deg) translateY(-5px);
      }
    }
  }
}
.youNeedIt {
  background-color: $blue;
  padding-bottom: 59px;
  position: relative;

  @media screen and (min-width: 320px) and (max-width: 1200px) {
    padding-bottom: 100px;
  }

  .stairs {
    position: absolute;
    width: 100%;
    top: 3px;
    .stairs_item {
      display: block;
      background-color: $pink;
      margin-bottom: 6px;
    }
    .stairs_item-two {
      height: 2px;
    }
    .stairs_item-four {
      height: 4px;
    }
    .stairs_item-eight {
      height: 8px;
    }
    .stairs_item:last-child {
      margin-bottom: 0;
    }
  }

  h3 {
    font: 900 32px niveau-grotesk;
    text-transform: uppercase;
    color: $grey1;
    padding-top: 99px;

    @media screen and (max-width: 768px) {
      font-size: 20.4px;
    }
  }
  .youNeedIt_body {
    position: relative;
    .cards {
      margin-top: 40px;
      justify-content: space-between;

      @media screen and (min-width: 320px) and (max-width: 1200px) {
        justify-content: center;
      }

      .card_item {
        width: 430px;
        height: 128px;
        background: $bloomarin;
        border-radius: 10px;
        margin: 0 5px 33px;

        @media screen and (min-width: 768px) and (max-width: 910px) {
          height: 158px;
          width: 349px;
        }
        @media screen and (min-width: 594px) and (max-width: 768px) {
          height: 102px;
          width: 272px;
        }
        @media screen and (min-width: 423px) and (max-width: 594px) {
          height: 157px;
          width: 187px;
          margin: 0 5px 10px;
        }
        @media screen and (min-width: 320px) and (max-width: 423px) {
          height: 116px;
          width: 334px;
          margin: 0 5px 10px;
        }

        h4 {
          font: 900 20px niveau-grotesk;
          line-height: 28px;
          letter-spacing: 0.3px;
          text-transform: uppercase;
          color: $grey1;
          margin: 0 5px 0 24px;
          @media screen and (max-width: 768px) {
            font-size: 14.04px;
          }
          @media screen and (min-width: 320px) and (max-width: 594px) {
            margin: 0 0 0 10px;
            line-height: 20px;
          }
        }
      }
    }
    .image {
      position: absolute;
      right: -7%;
      bottom: -23px;
      width: 237px;
      height: 222px;
      background: #fcbd3b;
      border-radius: 10px;

      @media screen and (min-width: 1270px) and (max-width: 1440px) {
        right: -5%;
      }
      @media screen and (min-width: 1200px) and (max-width: 1270px) {
        right: -3%;
        transform: scale(0.9);
      }
      @media screen and (min-width: 768px) and (max-width: 1200px) {
        bottom: -51%;
        right: 2%;
        transform: scale(0.9);
      }
      @media screen and (min-width: 423px) and (max-width: 768px) {
        bottom: -60%;
        right: 2%;
        transform: scale(0.8);
      }

      @media screen and (min-width: 320px) and (max-width: 423px) {
        bottom: -38%;
        right: -2%;
        transform: scale(0.7);
      }

      img {
        position: absolute;
        right: 14px;
        top: 12px;
        border-radius: 10px;
        border-bottom-left-radius: 50px;
      }
    }
  }
}

.migrations {
  padding-bottom: 150px;

  @media screen and (min-width: 320px) and (max-width: 540px) {
    padding-bottom: 0px;
  }
  .poster {
    margin-top: 186px;
    width: 100%;
    height: 660px;
    background: $pink;
    border: 6px solid #000000;
    box-sizing: border-box;
    border-radius: 20px;

    @media screen and (min-width: 768px) and (max-width: 992px) {
      width: 100%;
      margin: 200px auto 300px;
    }
    @media screen and (min-width: 540px) and (max-width: 768px) {
      width: 100%;
      margin: 200px auto 80px;
    }
    @media screen and (min-width: 320px) and (max-width: 540px) {
      width: 100%;
      margin: 200px auto 250px;
    }

    h3 {
      margin-top: 70px;
      @media screen and (min-width: 320px) and (max-width: 768px) {
        width: 80%;
        margin: 20px auto 10px;
        line-height: 1.25em;
      }
    }

    .poster_body {
      padding: 64px 116px 0;
      @media screen and (min-width: 840px) and (max-width: 1062px) {
        padding: 64px 50px 0;
      }
      @media screen and (min-width: 320px) and (max-width: 840px) {
        padding: 12px 0 0;
      }
      .list {
        padding-left: 28px;

        @media screen and (min-width: 768px) and (max-width: 992px) {
          margin: 0 auto 55px;
          width: 90%;
        }
        @media screen and (min-width: 600px) and (max-width: 768px) {
          margin: 0 auto 55px;
          width: 72%;
        }
        @media screen and (min-width: 320px) and (max-width: 600px) {
          margin: 0 auto 55px;
          width: 95%;
          padding-left: 25px;
        }

        .list_item {
          list-style: none;
          margin-bottom: 25px;
          position: relative;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            margin-bottom: 12px;
          }
          &:nth-child(2),
          &:nth-child(3) {
            &:after {
              content: "";
              position: absolute;
              top: 50%;
              left: -25px;
              transform: translateY(-50%);
              width: 20px;
              height: 3px;
              background-color: #000000;

              @media screen and (min-width: 320px) and (max-width: 768px) {
