        width: 20px;
        border-radius: 7px 0 0 7px;
        border-left: 3px solid #000;
        border-top: 3px solid #000;
        border-bottom: 3px solid #000;

        @media screen and (min-width: 605px) and (max-width: 991px) {
          left: 7%;
          top: 13px;
          bottom: 27%;
          width: 15px;
          border-left: 2px solid #000;
          border-top: 2px solid #000;
          border-bottom: 2px solid #000;
        }

        @media screen and (min-width: 605px) and (max-width: 768px) {
          left: 17%;
          top: 10px;
          bottom: 32%;
          width: 15px;
          border-left: 2px solid #000;
          border-top: 2px solid #000;
          border-bottom: 2px solid #000;
        }

        @media screen and (min-width: 605px) and (max-width: 768px) {
          left: 17%;
          top: 10px;
          bottom: 32%;
          width: 15px;
          border-left: 2px solid #000;
          border-top: 2px solid #000;
          border-bottom: 2px solid #000;
        }
        @media screen and (min-width: 600px) and (max-width: 605px) {
          left: 17%;
          top: 22px;
          bottom: 32%;
          width: 15px;
          border-left: 2px solid #000;
          border-top: 2px solid #000;
          border-bottom: 2px solid #000;
        }

        @media screen and (min-width: 538px) and (max-width: 600px) {
          left: 6%;
          top: 10px;
          bottom: 36%;
          width: 15px;
          border-left: 2px solid #000;
          border-top: 2px solid #000;
          border-bottom: 2px solid #000;
        }

        @media screen and (min-width: 462px) and (max-width: 538px) {
          left: 7%;
          top: 10px;
          bottom: 36%;
          width: 15px;
          border-left: 2px solid #000;
          border-top: 2px solid #000;
          border-bottom: 2px solid #000;
        }
        @media screen and (min-width: 368px) and (max-width: 462px) {
          left: 7%;
          top: 22px;
          bottom: 30%;
          width: 15px;
          border-left: 2px solid #000;
          border-top: 2px solid #000;
          border-bottom: 2px solid #000;
        }
        @media screen and (min-width: 320px) and (max-width: 368px) {
          top: 22px;
