          width: 15px;
          border-left: 2px solid #000;
          border-top: 2px solid #000;
          border-bottom: 2px solid #000;
        }
      }

      .card {
        width: 489px;
        height: 735px;
        background: #fc6839;
        border-radius: 20px;
        position: relative;

        @media screen and (min-width: 992px) and (max-width: 1062px) {
          width: 435px;
        }

        @media screen and (min-width: 768px) and (max-width: 992px) {
          width: 65%;
          margin: 0 auto;
        }

        @media screen and (min-width: 628px) and (max-width: 768px) {
          width: 80%;
          margin: 0 auto;
          height: 610px;
        }
        @media screen and (min-width: 468px) and (max-width: 628px) {
          width: 80%;
          margin: 0 auto;
          height: 550px;
        }

        @media screen and (min-width: 320px) and (max-width: 468px) {
          width: 99%;
          height: 517px;
          margin: 0 auto;
          margin-top: -25px;
        }

        .card_body {
          padding: 30px 50px 0;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            padding: 18px 15px 0;
          }
          h4 {
            font: 900 24px niveau-grotesk;
            line-height: 32px;
            letter-spacing: 0.3px;
            color: #ffffff;

            @media screen and (min-width: 320px) and (max-width: 768px) {
              font-size: 22.4px;
              line-height: 25px;
              text-align: center;
            }
          }
          p {
            font: 500 20px niveau-grotesk;
            line-height: 30px;
            margin: 24px 0 35px;
            letter-spacing: 0.3px;
            color: #ffffff;
            @media screen and (max-width: 768px) and (min-width: 320px) {
              font-size: 14.04px;
              line-height: 23px;
            }
          }

          a {
            padding: 12px 25px;
            background: #ffffff;
            border: 4px solid $grey1;
            border-radius: 10px;
            font: bold 14px niveau-grotesk;
            line-height: 20px;
            letter-spacing: 0.3px;
            text-transform: uppercase;
            color: $grey1;
            cursor: pointer;
            text-decoration: none;
            z-index: 2;
            position: relative;
            @media screen and (max-width: 768px) and (min-width: 320px) {
              padding: 10px 15px;
            }
          }

          .image {
            position: absolute;
            right: -1px;
            bottom: -2px;
            left: -1px;
            border-radius: 20px;
            overflow: hidden;
            z-index: 1;
            text-align: center;
            img {
              width: 100%;
              @media screen and (max-width: 962px) and (min-width: 768px) {
                width: 80%;
              }
            }
          }
        }
      }
    }
  }
}

