.page-contact {
    .question{
        margin-top: 0;
        padding-top: 80px;
    }

    .bg-wheat{
        padding-top: 80px;
    }
}

.devCenter{
    position: relative;
    z-index: 2;
    background: #fff;
    padding: 120px 0px;
    padding-bottom: 50px;
}

.tabsMaps{
    padding: 64px 55px;
    padding-bottom: 0;

}

.mapsMenu{
    display: flex;
    justify-content: space-between;
    padding-left: 0;
    list-style: none;

    &_item{
        display: flex;
        width: 48%;
        flex-direction: column;
    }

    &_inner{
        width: 100%;
        position: relative;
        z-index: 1;
        background: #FCBD3B;
        border-radius: 20px;
        padding: 25px 30px;
        padding-right: 0;
        padding-top: 80px;
    }

    &_title{
        text-transform: uppercase;
        font-weight: 900;
        color: #000;
        margin-bottom: 12px;
    }

    &_link{
        position: relative;
        width:145px;
        margin-left: 30px;
        height: 148px;
        z-index: 2;
        margin-bottom: -45px;
        border-radius: 10px 10px 10px 20px;
        
    }

    &_item.active &_link{

        &::after{
            content: ""; 
            top: -5%;
            bottom: 5%;
            right: -5%;
            left: 5%;
            position: absolute;
            z-index: -1;
            background: #FC6839;
            border-radius: 10px;
        }
    }

    &_img{
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        bottom: 0;
        left: 0;
        right: 0;

        border-radius: 10px 10px 10px 20px;

    }
}

.mapsMenu_list{
    padding-left: 0;
    list-style: none;
    margin-top: 10px;

    li{
        margin-top: 8px;
        display: flex;
        font-weight: 600;
        font-size: 0.7em;
        align-items: center;

        img {
            width: 100%;
            max-width: 20px;
            max-height: 30px;
            margin-right: 10px;
        }
    }
}

.mapsSlider{ 
    padding-top: 30px;

    .owl-dots{
        display: none;
    }

    &_item{
        padding-right: 15px;
        padding-bottom: 15px;
    }

    &_map{
        position: relative;
        border-radius: 10px 10px 25px 10px;
        overflow: hidden;
        img{
            width: 100%;
            height: auto;
        }
    }

    &_bg{
        content: ""; 
        position: absolute;
        z-index: -1;
        top: 5%;
        bottom: 0;
        right: 0;
        left: 2%;
        background: #FC6839;
        border-radius: 10px 10px 25px 10px;
    }
}
