// SERVICES
.services {
  @include servGradient; 
  padding-top: 96px;
  

  &--braun {
    @include servGradient-brown;
  }

  &_wrap{
    position: relative;
    margin-top: 48px;
  }
}

.headline {
  font-family: 'Brandon Grotesque';
  font-size: 1.6em;
  text-transform: uppercase;
  font-weight: 900;
  margin-bottom: 0;
}

.stairs {
  position: relative;
  background-color: #F6F0E1;
  padding-bottom: 6px;

  &-modern{
    background-color: #fff;
  }

  &-tech {
    background-color: #F6F0E1;

    .stairs_item {
      background-color: #F7CA59;
    }
  }
  
  
  &_item{
    display: block;
    background-color: #A6D7C9;
    margin-bottom: 6px;

    &:last-child{
      margin-bottom: 0;
    }

    &-two{
      height: 2px;
    }

    &-four{
      height: 4px;
    }

    &-eight{
      height: 8px;
    }
  }
  
  &_item-modern{
    background-color: #F7CA59;
  }

  &_item-white{
    background-color: #fff;
  }
}

.serviceItem{
  display: flex;
  position: relative;
  z-index: 2;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-height: 272px;
  background: #BAE8DB;
  border-radius: 10px;
  padding-bottom: 19px;
  padding-right: 19px;
  padding-left: 32px;
  padding-top: 32px;

  transition: all 0.3s;

  &::after{
    content: "";
    border-radius: 10px;
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    background: #BAE8DB;
    transition: all 0.3s;
  }

  &:hover{
    text-decoration: none;
    cursor: pointer;
    background-color: #F7CA59;

    .btnEnter{
      box-shadow: none;
      top: 3px;
      left: 3px;
    }

    .btnEnter_rotate{
      box-shadow: none;
    }

    &::after{
      top: -20px;
      bottom: -20px;
      background-color: #F7CA59;
      transition: all 0.3s;
    }

  }

  &_code{
    font-family: 'Fira Code';
    font-weight: 300;
    color: #888888;
    background-color: #fff;
    border-radius: 35px;
    padding: 2px 11px;
    padding-bottom: 1px;
    margin-bottom: 32px;
  }

  &_title{
    font-weight: 900;
    color: $base-font-color;
    margin-bottom: 0;
    text-transform: uppercase;
  }
}

.btnEnter{
  position: relative;
  width: 65px;
  margin-top: auto;
  margin-left: auto;
  height: 32px;
  top: 0;
  left: 0;
  
 
  @include btnEnter;

  &_rotate{
    position: absolute;
    z-index: 1;
    transform: rotate(90deg) scale(1,-1);
    bottom: 4px;
    right: -12px;
    height: 32px;
    width: 47px;

    @include btnEnter;
    
  }

  &_img{
    width: 23px;
    margin-bottom: 8px;
  }

  &_icon{
    position: absolute;
    z-index: 2;
    left: 8px;
    top: 0;
    width: 45px;
    height: 24px;
    background-color: #fff;
    border-radius: 10px;
  }
}

// END SERVICES

