.modernization{
    position: relative;
    z-index: 2;

    &_title{
        text-align: center;
        text-transform: uppercase;
        font-size: 1.6em;
        margin-bottom: 35px;
        font-weight: 900;
        user-select: none; 
    }
    &_item{
        display: flex;
        align-items: center;
        margin-top: 20px;
        user-select: none;
    }

    &_label{
        position: relative;
        width: 16px;
        height: 16px;
        margin-right: 16px;
        margin-bottom: 0;
        border: 2px solid #333333;
        border-radius: 3px;
        background-color: #fff;
        cursor: pointer;
    }

    &_input{
        display: none;
    }

    &_check{
        position: absolute;
        width: 12px;
        top: -8px;
        margin-left: -6px;
        left: 50%;
        visibility: hidden;

        img{
            width: 100%;
            height: auto;
        }
    }

    &_text{
        font-weight: 500;
        margin-bottom: 0;
        cursor: pointer;
    }

    &_wrapp{
        margin-top: 35px;
        text-align: center;

    }
}

.modernization_input:checked + label{
    background-color: #FC6839;
    border: none;
}

.modernization_input:checked + label img{
    visibility: visible;
  }

.imgCircle{
      position: absolute;
      z-index: 2;
      left: -167px;
      bottom: -22px;
}

.modernWorks{
    padding: 100px 0px;
    padding-bottom: 260px;

    &-mt{
        padding-top: 100px;
    }
}

