.approachSchema{
    max-width: 410px;
    margin-left: 50px;
    padding-bottom: 20px;
}

.schema{
    position: relative;
    max-width: 400px;    
    
    &_inner{
        display: flex;
        justify-content: center;

        max-width: 400px;
        height: 305px;
        position: relative;
        z-index: 2;
        border-radius: 20px;
        border: 4px solid $base-font-color;
        background-color: #fff;

        &::after{
            position: absolute;
            z-index: 2;
            content: "";
            width: 4px;
            top: 0;
            bottom: -36px;
            background-color: $base-font-color;
            left: 50%;
            margin-left: -2px;
        }
    }

    &_cloud{
        position: absolute;
        z-index: 3;
        left: 50%;
        top: 50%;
        margin-top: -20px;
        margin-left: -35px;

    }

    &_hosting{
        position: absolute;
        height: 70px;
        z-index: 1;
        top: 34px;
        left: -82px;
        transform: rotate(-90deg);
        padding: 12px 37px;
        background-color: $base-font-color;
        color: #fff;
        text-transform: uppercase;
        font-weight: 900;
        font-size: 14px;
      
        border-radius: 20px 20px 0 0;
    }

    &::after{
        content: "";
        background-color: $base-font-color;
        position: absolute;
        z-index: 1;
        width: 100%;
        left: 16px;
        right: 8px;
        bottom: -16px;
        top: 20px;
        border-radius: 20px;
    } 

    &_body{
        position: relative;
        top: 0px;
        margin: auto;
        width: 73%;
        display: flex;
        justify-content: space-between;
    }

    &_img{
        width: 73px;
         height: 47px;
    }
}

.core{
    text-align: center;

    &_title{
        display: block;
        font-size: 14px;
        margin-top: 15px;
        line-height: 1.3em;
        font-weight: 900;
    }

    &_img{
        width: 65px;
        height: 65px;
    }
}


.apiBlock{
    margin-top: 32px;
    display: flex;
    justify-content: space-between;

    &_title{
        position: relative;
        z-index: 2;
        display: block;
        font-weight: 900;
        font-size: 14px;
        text-transform: uppercase;
        padding: 23px 36px;
        border-radius: 10px;
        border: 4px solid $base-font-color;
        background-color: #fff;
        
        &-pd{
            padding-left: 16px;
            padding-right: 16px;
        }
    }

    &_item{
        position: relative;
        z-index: 1;

        &::after{
            content: "";
            background-color: $base-font-color;
            position: absolute;
            z-index: 1;
            width: 100%;
            left: 6px;
            right: 8px;
            bottom: -6px;
            top: 8px;
            border-radius: 10px;
        }

        &::before{
            position: absolute;
            left: -8px;
            content: "";
            top: 50%;
            margin-top: -2px;
            width: 10px;
            height: 4px;
            background-color: $base-font-color;
        }

        &:first-child::before{
            display: none;
        }
    }
}

.borderAnimate{
    border-color: #FC6839;
    &.schema_hosting{
        background-color: #FC6839;
        animation: glowHosting .25s infinite alternate; 
    }

    &.schema_img{
        animation: beat .25s infinite alternate;
 	    transform-origin: center;
    }

    &.core_img{
        animation: beat .25s infinite alternate;
        transform-origin: center;
    }

    &.core_title{
        color: #FC6839;
        animation: glowText .25s infinite alternate;
        transform-origin: center;
    }
    
    &.schema_inner{
        animation: glowBorder .25s infinite alternate;
        transform-origin: center;
    }   
   
    

    &::before{
        background-color: #FC6839;
    }

    &-middle{
        &::after{
            background-color: #FC1E1E;
        }
    }

    &-n {
        &::before{
            background-color: $base-font-color;
        }
    }

    .apiBlock_title{
        color: #FC6839;
        border-color: #FC6839;
        animation: glow .25s infinite alternate;
        transform-origin: center;       
    }
}

