@keyframes beat{
	to { transform: scale(1.03); }
}

@keyframes glow {
    to { box-shadow: -2px -1px 18px -5px rgba(252,104,57,1);
        border-color:#FC1E1E;
        color: #FC1E1E;
     }
}

@keyframes glowHosting {
    to { box-shadow: -2px -1px 18px -5px rgba(252,104,57,1);
        background-color:#FC1E1E;
     }
}

@keyframes glowText{
    to { 
        color: #FC1E1E;
    }
}
@keyframes glowBorder {
    to { 
        box-shadow: -2px 2px 29px -8px rgba(252,104,57,1);
        border-color:#FC1E1E;
    }
}

