// PROCESS.EXE

.quize-process .process {
    padding: 45px 56px;
}

.process_step{
    font-family: Fira Code;
    font-size: 1.4em;
}

.processSlider{
    margin-top: 40px;

    &_flex{
        display: flex;
        justify-content: space-between;
    }

    &_wrap{
        width: 70%;
    }

    &_desc{
        font-weight: 500;
    }
}

.processSlider_title{
    margin-bottom: 24px;
    font-weight: 800;
    text-transform: uppercase;
    font-size: 1.6em;
    line-height: 1.4em;
}

.processSlider_img {
    max-height: 99px;
    width: auto;
}

.processLoading {
    display: flex;
    margin-top: 50px;
    background-color:#F6F0E1;;
    padding: 6px;
    border-radius: 5px;

    .step {
        display: flex;
        flex-grow: 1;

        &-last {
            margin-right: 0;
        }

        span {
            margin-right: 6px;
            border-radius: 2px;
            height: 20px;
            flex-grow: 1;
            background-color: #A6D7C9;
            opacity: 0;
        }
    }
}

.step.active{

    span{
        animation-name: fadeIn;
        animation-duration: 0.2s;
        animation-fill-mode: forwards;

        &:nth-child(2){
            animation-delay:0.2s;
        }
        &:nth-child(3){
            animation-delay:0.3s;
            
        }
        &:nth-child(4){
            animation-delay:0.4s;
        }
        &:nth-child(5){
            animation-delay:0.5s;
        }
        &:nth-child(6){
            animation-delay:0.6s;
        }
        &:nth-child(7){
            animation-delay:0.8s;
        }

    }
}

.step.active-not{
    span {
        opacity: 1;
    }
    
    span{
        animation-name: fadeOut;
        animation-duration: 0.2s;
        animation-fill-mode: forwards;

        &:first-child{
            animation-delay:0.8s;
        }
        &:nth-child(2){
            animation-delay:0.7s;
        }
        &:nth-child(3){
            animation-delay:0.6s;
        }
        &:nth-child(4){
            animation-delay:0.5s;
            
        }
        &:nth-child(5){
            animation-delay:0.4s;
        }
        &:nth-child(6){
            animation-delay:0.3s;
        }
        &:nth-child(7){
            animation-delay:0.2s;
        }
        &:nth-child(8){
            animation-delay:0.1s;
        }
    }
}

.processMinute {
    font-family: Fira Code;
    font-size: 0.5em;
    font-weight: 500;
    margin: 0;
    margin-top: 16px;
}

.processBtn {
    display: flex;
    margin-top: 40px;
    justify-content: flex-end;
    align-items: center;

    .btn{
        font-size: 1em;
        font-weight: 600;
        margin-left: 25px
    }

    .btn-process:hover {
        color: #fff;
    }

    img {
        width: 10px;
        margin-right: 10px;
    }
}

