.tech-schema {
  background: #F6F0E1;
  padding: 130px 0px;
}

.approachSchema--tech {
  max-width: none;
  margin-top: 67px;
  padding-right: 47px;

  .schema {
    max-width: none;
  }

  .schema_hosting {
    left: -90px;
  }

  .schema--tech {
    width: 100%;
  }

  .schema_hosting {
    top: 42px;
  }

  .schema_inner {
    justify-content: flex-start;
    max-width: none;
    height: auto;

    &::after {
      display: none;
    }
  }
}

.schema-map {
  width: 60%;
  padding-bottom: 115px;


  &_inner {
    position: relative;
    padding-top: 105px;
    padding-bottom: 48px;
    display: flex;
    justify-content: center;

    &::after {
      content: "";
      position: absolute;
      width: 4px;
      background-color: #333333;
      top: 0;
      bottom: 0;
      left: 50%;
      margin-left: -2px;
    }
  }

  &_icn {
    max-width: 107px;
  }

  &_title {
    text-align: center;
    text-transform: uppercase;
    font-weight: 900;
    display: block;
    margin-top: 27px;
    line-height: 1.5em;
    font-size: 16px;
  }

  &_arrow {
    position: relative;
    z-index: 2;
    width: 70px;
    height: 70px;
    margin: 4px 50px;
    border-radius: 50%;
    display: flex;
    background-color: #FCBD3B;
  }

  &_arrow-img {
    max-width: 40px;
    margin: auto;
  }
}

.apiBlock--tech {
  justify-content: center;
  align-items: stretch;
  margin-top: 0;

  .apiBlock_item--cloud {
    width: 100%;
    max-width: 215px;
    margin: 0px 14px;
  }

  .apiBlock_item::before {
    top: 60%;
    background-color: #F7CA59;
  }

  .apiBlock_title {
    width: 100%;
    border: none;
    text-align: center;
    background-color: #F7CA59;
  }

  .apiBlock_img {
    max-height: 76px;
  }

  .apiBlock_desc {
    display: block;
    margin-top: 22px;
    text-align: center;
    font-size: 13px;
  }
}

.schema-list {
  width: 40%;
  margin-top: 63px;

  &_title {
    font-size: 34px;
    font-family: 'Brandon Grotesque';
    text-transform: uppercase;
    font-weight: 900;
  }

  &_inner {
    padding-left: 0;
  }

  &_item {
    display: flex;
    margin-top: 35px;
    align-items: center;
    font-size: 24px;
    font-weight: 500;

    img {
      width: 20px;
    }

    span {
      margin-left: 20px;
    }
  }
}

.casearousel-tech{
  position: relative;

  &_img {
    position: absolute;
    top: -65px;
    left: 64px;
    z-index: 2;
    max-width: 390px;
  }

  .casearousel {
    min-height: auto;
    padding-bottom: 35px;
  }
}

.services-tech {
  background: #F7CA59;

  .serviceItem::after {
    background-color: #F7CA59;
  }

  .serviceItem {
    padding-left: 15px;
    padding-right: 15px;
    &:hover {
      cursor: default;

      &::after{
        top: 0px;
        bottom: 0px;
      }
    }
  }

  .btnEnter {
    display: none;
  }
}

.migration-cloud {
  background: #F6F0E1;
  border-radius: 5%;
  padding: 60px;

  &_title {
    display: flex;
    margin-bottom: 35px;

    img {
      max-width: 120px;
    }

    span {
      font-size: 32px;
      font-weight: 900;
      font-family: 'Brandon Grotesque';
      line-height: 1.5em;
      margin-left: 25px;
    }
  }
}

.cloud-list {
  list-style: none;
  padding-left: 0;

  &_item {
    display: flex;
    padding: 52px 0;
    border-bottom: 2px solid #C4C4C4;

    &:last-child {
      border-bottom: none;
    }
  }

  &_img {
    max-width: 120px;
  }

  &_title {
    font-weight: 900;
    margin-left: 55px;
    font-size: 34px;
    line-height: 1.5em;
    text-transform: uppercase;
  }
}

.migration-section {
  padding: 160px 0px;
  background-color: #F7CA59;

  &_wrapper {
    margin-top: 85px;
  }

}

.migration-process {
  display: flex;
  flex-direction: column;
  height: 100%;
  &_list {
    flex-grow: 1;
    height: 100%;
    position: relative;
    min-height: 190px;
    background-color: #F6F0E1;
    padding: 16px;
    border-radius: 4px;
    list-style: none;
    font-size: 18px;
    margin: 0;
    margin-bottom: 30px;

    &::after {
      content: '';
      position: absolute;
      bottom: -23px;
      left: 15px;
      border: 12px solid transparent;
      border-top: 12px solid #F6F0E1;
    }
  }

  &_item {
    font-weight: 600;
    margin-bottom: 3px;
  }

  &_title {
    font-size: 24px;
    font-weight: 900;
  }

  &--reverse {
    .migration-process_title {
      display: flex;
      align-items: center;
      min-height: 45px;
    }
    .migration-process_list {

      margin: 0;
      margin-top: 30px;

      &::before {
        content: '';
        position: absolute;
        top: -23px;
        left: 15px;
        border: 12px solid transparent;
        border-bottom: 12px solid #F6F0E1;
      }

      &::after {
        display: none;
      }
    }
  }
}

.migration-process-bar {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
  padding-bottom: 20px;
  display: flex;

  li {
    position: relative;
    font-family: 'Fira Code';
    font-weight: 200;
    width: 112px;
    font-size: 16px;
    margin-top: 50px;
    margin-bottom: 50px;
    line-height: 1.5em;
    padding-left: 24px;
    background-color: #BAE8DB;
    margin-right: 30px;
  }

  li:nth-child(2n) {
    background-color: #FC6839;
    color: #fff;
  }

  li:last-child {
    background-color: #FC6839;
    margin-right: 0;

    &::after {
      background-color: #F7CA59;
      content: '';
      position: absolute;
      right: -13px;
      right: 0;
      border: 12px solid transparent; border-left: 12px solid #FC6839;
    }
  }
}

.blog-tech {
  background-color: #F6F0E1;
  padding: 160px 0;
}

.blog-list {
  padding-left: 0;
  list-style: none;

  &_item {
    display: flex;
    justify-content: space-between;
  }

  &_img {
    width: 30%;
    height: 130px;
    overflow: hidden;

    img {
      width: 100%;
      height: auto;
    }
  }

  &_content {
    width: 80%;
    margin-left: 25px;
  }

  &_title {
    font-size: 24px;
  }
}

.CatArticle_inner--tech {

   &::after {
     content: "";
     position: absolute;
     left: 0;
     right: 0;
     top: auto;
     bottom: -15px;
     height: 3px;
     width: 100%;
     background-color: #333333;
   }

  .articelColumn_wrapp {
    width: 74%;
  }
}

.modern-lang-section {
  background-color: #A6D7C9;
  padding: 160px 0;
}

.modern-lang {
  padding: 64px;
  border-radius: 20px;
  background-color: #F6F0E1;

  &_header {
    display: flex;
  }

  &_img {
    max-width: 108px;
  }

  &_title {
    font-weight: 900;
    font-size: 32px;
    margin-left: 30px;
    text-transform: uppercase;
    font-family: 'Brandon Grotesque';
  }

  &_desc {
    font-weight: 500;
    margin-top: 40px;
  }

  &_list {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
      font-weight: 700;
      font-size: 24px;
      margin-bottom: 32px;
    }

    li:last-child {
      margin-bottom: 0;
    }
  }
}

.migration-method {
  margin-top: 160px;
  margin-bottom: 160px;
}

.migration-method-table {
  margin-top: 88px;
}

.migration-arrow {
  display: block;
  position: relative;
  font-size: 24px;
  font-weight: 900;
  padding: 6px 0px;
  text-align: center;
  color: #fff;
  background-color: #FC6839;

  &::after {
    content: '';
    position: absolute;
    background-color: #fff;
    top: 0;
    right: -30px;
    border: 20px solid transparent; border-left: 10px solid #FC6839;
  }
  &::before {
    content: '';
    position: absolute;
    left: 0px;
    top: 0;
    border: 20px solid transparent; border-left: 10px solid #fff;
  }

  &-light-green {
    background-color: #BAE8DB;
    font-size: 14px;
    color: #333333;
    text-transform: uppercase;

    &::after {
      border-left-color: #BAE8DB;
    }
  }
}

.migration-arrow_inner {
  display: flex;
  margin-top: 16px;
  justify-content: space-between;

  span {
    width: 47%;
  }
}

.migration-card {
  background-color: #F6F0E1;
  border-radius: 4px;
  margin-top: 16px;
  padding: 32px;
  padding-bottom: 20px;

  &_header {
    display: flex;
    min-height: 190px;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 20px;
    border-bottom: 1px solid #DBD0B4;
  }

  &_title {
    font-size: 24px;
    margin-bottom: 20px;
    font-weight: 700;
    text-align: center;
  }

  &_subtitle {
    font-size: 16px;
    color: #FC6839;
    text-align: center;
    text-transform: uppercase;
    font-weight: 900;
  }

  &_list {
    min-height: 360px;
    list-style: none;
    padding-left: 0;
    margin-top: 16px;

    li:before {
      position: absolute;
      left: 0;
      top: 0;
      content: "";
      color: #FC6839;
      margin-right: 6px;
    }

    li {
      position: relative;
      font-size: 18px;
      margin: 10px 0px;
      padding-left: 15px;
      font-weight: 700;
    }
  }
}

.app-mod {
  margin-top: 50px;

  &_title{
    display: block;
    text-transform: uppercase;
    font-weight: 900;
    margin-bottom: 20px;
  }

  &_list {
    li {
      font-weight: 500;
      margin: 10px 0px;
    }
  }
}


.afterTransition_first{
  position: static;
    
  @media (min-width: 768px) and (max-width: 962px){
    margin-bottom: 40px;
  }
}

.note {
  position: relative;
  z-index: 1;
  top: 50px;
  max-width: 270px;
  transform: rotate(-4deg);
  background-repeat: no-repeat;
  
  @media (min-width: 768px) and (max-width: 962px){
    position: absolute;
    right: 0px;
    bottom: 0px;
    top: auto !important;
  }

  &_header {
    background-size: contain;
    background-repeat: no-repeat;
    height: 74px;
  }

  &_title {
    position: relative;
    top: -29px;
    z-index: 2;
    font-size: 24px;
    font-weight: 900;
    display: block;
    padding-left: 20px;
  }

  &_desc {
    position: relative;
    top: -8px;
    margin-left: 1px;
    margin-right: 8px;
    background-color: #BAE8DB;
    border: 7.5px solid #000000;
    border-top: none;
    border-bottom-width: 4px;
    border-left-width: 4px;
    border-radius: 0px 0px 10px 10px;
    padding-top: 4px;
    &:after {
      content: "";
      width: 100%;
      position: absolute;
      left: 15px;
      right: 0px;
      bottom: -15px;
      z-index: -1;
      top: 0px;
      background: #000000;
      border-radius: 0px 0px 6px 6px;
    }
  }

  &_text {
    position: relative;
    font-size: 14px;
    top: -20px;
    padding: 0 20px;
    padding-bottom: 5px;
    margin-bottom: 0;
    font-weight: bold;
    line-height: 18px;
  }
}

//MEDIA
@media (max-width: 960px) {
  .approachSchema--tech {
    padding-right: 15px;
  }

  .casearousel-tech_img {
    max-width: 200px;
    top: -37px;

    img {
      width: 100%;
      height: auto;
    }
  }

  .approachSchema--tech .schema_inner {
    flex-direction: column;
  }

  .schema-map {
    width: 100%;
    padding-bottom: 15px;
  }

  .schema-list {
    width: 100%;
    margin-top: 15px;
    margin-left: 30px;
  }

  .cloud-list_img {
    max-width: 55px;
  }

  .cloud-list_title {
    font-size: 16px;
  }

  .migration-cloud_title span {
    font-size: 16px;
  }

  .migration-cloud_title img {
    max-height: 55px;
  }

  .migration-cloud {
    padding: 15px;
  }

  .cloud-list_item {
    padding: 20px 0;
  }
}

@media (max-width: 770px){
  .modern-lang-section {
    padding: 50px 0px;
  }

  .migration-card_title {
    line-height: 1.2em;
    font-size: 20px;

    br {
      display: none;
    }
  }

  .migration-card {
    margin-bottom: 15px;
    padding: 15px;
  }

  .migration-card_subtitle {
    font-size: 14px;
  }

  .migration-method {
    margin-top: 50px;
    margin-bottom: 50px;
  }

  .migration-card_header {
    min-height: auto;
  }

  .migration-card_list {
    min-height: auto;
  }

  .migration-arrow {
    font-size: 11px;
    text-transform: uppercase;
    padding-left: 12px;
    padding-right: 6px;
    &:after {
      right: -25px;
      border-width: 16px;
      border-left-width: 11px;
    }
  }

  .migration-method-table {
    margin-top: 0;
  }

  .modern-lang {
    padding: 15px;
    margin: 0px 15px;
  }

  .modern-lang_img {
    max-width: 60px;
  }

  .modern-lang_header {
    align-items: center;
  }

  .modern-lang_desc {
    margin-top: 15px;
  }

  .modern-lang_list li {
    font-size: 16px;
    margin-bottom: 15px;
  }

  .modern-lang_title {
    font-size: 16px;
  }

  .tech-schema {
    padding: 55px 0px;
  }

  .migration-process_list {
    margin-top: 15px;
    margin-bottom: 15px;
    min-height: auto;
  }

  .migration-process--reverse .migration-process_list {
    margin-top: 15px;
  }

  .migration-process.migration-process--reverse {
    margin-top: 15px;
  }

  .migration-process_title {
    font-size: 20px;
  }

  .migration-process-bar {
    margin-top: 15px;

    li {
      display: none;
    }

    background-color: #FC6839;
  }

  .migration-section_wrapper {
    margin-top: 20px;
  }

  .schema-list_inner {
    padding-left: 20px;
  }

  .schema-list_title {
    text-align: center;
    font-size: 16px;
  }

  .schema-list_item {
    font-size: 14px;
    margin-top: 10px;
  }

  .schema-map_inner {
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .help-tech {
    padding-bottom: 25px;
  }

  .migration-section {
    padding: 50px 0px;
  }

  .blog-tech {
    padding: 50px 0;
  }

  .note {
    top: 0;
  }
}

@media (max-width: 540px) {
  .migration-arrow {
    font-size: 14px;
    text-transform: uppercase;
  }
  
  .migration-arrow:after {
    border-width: 13px 16px 16px 11px;
  }
  .CatArticle_inner--tech {
    margin-top: 25px;
  }

  .schema-map_icn {
    max-width: 50px;
  }

  .legacy.legacy-tech {
    margin-top: 25px;
  }

  .schema-list {
    margin-left: 0;
    margin-top: 0;
  }

  .schema-map_title {
    font-size: 10px;
  }

  .schema-map_arrow {
    width: 35px;
    height: 35px;
  }

  .schema-map_arrow-img {
    max-width: 25px;
  }

  .approachSchema--tech .schema_hosting {
    top: -28px;
    left: 0;
  }

  .apiBlock.apiBlock--tech {
    margin-top: 0;
    padding: 0 4px;
  }

  .apiBlock--tech .apiBlock_img {
    max-height: 30px;
  }

  .apiBlock--tech .apiBlock_title {
    padding: 5px;
  }

  .apiBlock--tech .apiBlock_desc {
    font-size: 10px;
    margin-top: 5px;
  }

  .apiBlock_item::after {
    display: none;
  }

  .apiBlock--tech .apiBlock_item::before {
    top: 45%;
  }

  .apiBlock--tech .apiBlock_item--cloud {
    margin: 0 5px;
  }

}





