#header-stick{
  position: fixed;
  z-index: 99;
  width: 100%;
  background: #F6F0E1;
}

.header-static{
  box-shadow: none;
  
  .header-border{
     border-bottom: 4px solid $base-font-color;
  }

  .btn-contact{
    @include button;
    top: 0px;

  }   
}

.header-fixed{
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.14);

  .header-border{
    border-bottom: none;
  }

  .btn-contact{
    box-shadow: 0 0 0 -1px #333;
    transition: all .3s;
    color: #333;
    text-decoration: none;
    top: 0;
    left: 0;
  }  
  
}

.logo{
    display: block;
    position: relative;
    bottom: -11px;

    &_img{
        width: 200px;
    }
}

.menu{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family:"poynter-oldstyle-display";

    &_wrapper{
        display: flex;
        padding-top: 34px;
        padding-bottom: 34px;
        margin-left: auto;
        margin-bottom: auto;
        margin-top: auto;
        margin-right: 8%;
        list-style: none;
        padding-left: 0;
    }

    &_item--lvl_1{
      position: relative;
      padding-left: 60px;

      &:first-child{
        position: relative;
        padding-left: 0;
      }

      .menu_link{
        color: $base-font-color;
        font-weight: 700;
        transition: color 0.3s;

        .menu_arrow {
          fill: #333333;
          transition: fill 0.3s;
        }

        &:hover{
            color: #FC6839;
            fill: #FC6839;
            text-decoration: none;
            transition: color 0.3s;

            .menu_arrow{
                fill: #FC6839;
                transition: fill 0.3s;
            }
        }
      }
    }


      

    &_item--lvl_2{
        text-align: left;
        margin-bottom: 20px;
        
        &:last-child{
          margin-bottom: 0;
        }

        .menu_link{
          color: $base-font-color;
          font-size: 18px;
          font-weight: 600;
          transition: color 0.3s;
          
          .menu_arrow {
            fill: #333333;
          }
  
          &:hover{
              color: #FC6839;
              fill: #FC6839;
              text-decoration: none;
              transition: color 0.3s;
  
              .menu_arrow{
                  
                  transition: color 0.3s;
              }
          }          
      }
    }

    &_arrow{
        margin-top: 3px;
        margin-left: 6px;
        fill: $base-font-color;
        transition: color 0.3s;        
    }
}

.menu-link--active {
    color: #FC6839!important;
    fill: #FC6839;
}

.menuDropdown{
    display: none;
    position: absolute;
    top: 40px;
    z-index: 5;
    left: -50%;
    min-width: 312px;
    margin-left: 8px;
    list-style: none;
    padding: 35px 26px;
    background-color: #fff;
    border-radius: 10px;
    border: 4px solid $base-font-color;

    font-family: 'Brandon Grotesque';
    font-weight: 400;

    -webkit-box-shadow: 7px 6px 0px -1px $base-font-color;;
    -moz-box-shadow: 7px 6px 0px -1px $base-font-color;
    box-shadow: 7px 6px 0px -1px $base-font-color;

    &::after{
        content: ''; 
        position: absolute;
        right: 136px;
        z-index: 5;
        top: -24px;
        border: 12px solid transparent; 
        border-bottom: 12px solid $base-font-color;
    }

    &::before{
        content: ''; 
        position: absolute;
        z-index: 6;
        right: 137px;
        top: -17px;
        border: 11px solid transparent;
        border-bottom: 11px solid #fff;
    }

   
}

.btn{
    @include button;

    &-quize{
        padding: 14px 38px;
        font-size: 1em;
    }

    &-red{
        background: #FC6839;
        color: #fff!important; 
    }

    &-modern{
      width: 308px;
      padding: 15px 38px;
      font-size: 0.9em;
      text-align: center;
    }

    &-subscribe{
      margin-top: 10px;
    }

    &-process{
      background-color:#B7B7B7;
      color: #fff;
      display: flex;

      &.active {
        background-color: #FC6839;
      }

    }
}

#menu{
    display: none;
    width: 40px;
    height: 25px;
    position: relative;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .5s ease-in-out;
    -moz-transition: .5s ease-in-out;
    -o-transition: .5s ease-in-out;
    transition: .5s ease-in-out;
    cursor: pointer;
}

#menu span {
    display: block;
    position: absolute;
    height: 4px;
    width: 50%;
    background: #FC6839;;
    opacity: 1;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    -moz-transition: .25s ease-in-out;
    -o-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
  }
  
  #menu span:nth-child(even) {
    left: 50%;
    border-radius: 0 9px 9px 0;
  }
  
  #menu span:nth-child(odd) {
    left:0px;
    border-radius: 9px 0 0 9px;
  }
  
  #menu span:nth-child(1), #menu span:nth-child(2) {
    top: 0px;
  }
  
  #menu span:nth-child(3), #menu span:nth-child(4) {
    top: 10px;
  }
  
  #menu span:nth-child(5), #menu span:nth-child(6) {
    top: 20px;
  }
  
  #menu.open span:nth-child(1),#menu.open span:nth-child(6) {
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  
  #menu.open span:nth-child(2),#menu.open span:nth-child(5) {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
  }
  
  #menu.open span:nth-child(1) {
    left: 5px;
    top: 7px;
  }
  
  #menu.open span:nth-child(2) {
    left: calc(50% - 5px);
    top: 7px;
  }
  
  #menu.open span:nth-child(3) {
    left: -50%;
    opacity: 0;
  }
  
  #menu.open span:nth-child(4) {
    left: 100%;
    opacity: 0;
  }
  
  #menu.open span:nth-child(5) {
    left: 5px;
    top: 17px;
  }
  
  #menu.open span:nth-child(6) {
    left: calc(50% - 5px);
    top: 17px;
  }