.historyMenu{
    display: flex;
    list-style: none;
    padding-left: 10px;
    margin-bottom: -4px;

    &_item{
        position: relative;
        width: 104px;
        height: 60px;
        transform: perspective(100px) rotateX(16deg);
        

        margin-right: 10px;
        padding: 13px 0 17px 20px;
        font-weight: 900;
        border-radius: 8px 8px 0 0;
        
        cursor: pointer;
        background-color: #FCBD3B;

        border: 6px solid #333;

        &-plus{
            width: 73px;
            padding-left: 16px;
            font-size: 50px;
            font-weight: 500;           
        }
        

        &.active{            
            background: #F7CA59;
            border-width: 7px;
            margin-left: 5px;
            border-bottom-color: #F7CA59;

            &::after{
                content: "";
                position: absolute;
                left: -7px;
                bottom: -7px;
                width: 7px;
                height: 10px;
                border-radius: 0px 0px 5px 0px;
                background-color: #333;
            }

            &::before{
                content: "";
                position: absolute;
                right: -7px;
                bottom: -7px;
                width: 7px;
                height: 10px;
                border-radius: 0px 0px 0px 5px;
                background-color: #333;
            }
            
            span{
                position: static;
                top: 15px;
                transform: none;

                &::after {
                    
                    content: "";
                    position: absolute;
                    background-color: #F7CA59;
                    height: 2px;
                    left: -4px;
                    right: -4px;
                    bottom: -9px;
                }
            }

            &.historyMenu_item-plus {
                padding-left: 15px;                
            }
        }

        span{
            position: static;
            top: 13px;
            left: 20px;
            // transform: skew(-10deg);
        }
    }
}

