$grey1: #333333;
$pink: #f6f0e1;
$white: #ffffff;
$yellow: #f7ca59;
$blue: #a6d7c9;
$bloomarin: #bae8db;

.am-welcome {
  position: relative;
  padding-top: 130px;
  background: $pink;
  padding-bottom: 108px;

  @media (max-width: 767px) {
    padding-top: 35px;
  }

  .block-wrap {
    justify-content: space-between;

    @media screen and (min-width: 320px) and (max-width: 1145px) {
      justify-content: center;
    }
  }

  .am-welcome-service {
    overflow: hidden;
    padding-top: 30px;
  }

  h1 {
    font: 60px Playfair Display;
    line-height: 120%;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    margin: 0;

    @media (max-width: 767px) {
      font-size: 26.4px;
    }
  }

  p {
    font: normal 20px niveau-grotesk;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.3px;
    margin-top: 40px;
    color: $grey1;

    @media screen and (max-width: 767px) {
      font-size: 12px;
      margin: 20px auto 0;
      text-align: left;
    }
  }

  .block-wrap {
    position: relative;
    height: auto;
    margin: 33px auto 0;
    z-index: 0;

    .block-content {
      .block-about {
        margin-left: 52px;
        width: 1055px;
        height: 510px;
        background: $white;
        border: 4px solid $grey1;
        box-sizing: border-box;
        border-radius: 20px;
        z-index: 1;

        @media screen and (min-width: 992px) and (max-width: 1145px) {
          width: 1000px;
        }

        @media screen and (min-width: 550px) and (max-width: 1200px) {
          height: auto;
          width: 80%;
          margin: 0 auto;
        }

        @media screen and (min-width: 320px) and (max-width: 550px) {
          height: auto;
          width: 99%;
          margin: 0 auto;
        }

        .block-about_body {
          margin: 0 32px;

          @media screen and (min-width: 320px) and (max-width: 767px) {
            margin: 0 2px;
          }

          .am-schema_inner {
            position: relative;
            padding-top: 60px;
            @media screen and (min-width: 990px) and (max-width: 1200px) {
              padding: 73px 73px 0;
              height: 460px;
            }
            @media screen and (min-width: 767px) and (max-width: 990px) {
              padding: 60px 20px 0;
              height: 440px;
            }
            @media screen and (min-width: 500px) and (max-width: 767px) {
              padding: 40px 20px 0;
              height: 385px;
              margin-bottom: -45px;
            }
            @media screen and (min-width: 320px) and (max-width: 500px) {
              padding: 65px 0px 0;
              height: 320px;
              margin-bottom: -45px;
            }

            .blockBorder {
              position: absolute;
              top: 55%;
              left: 50%;
              transform: translate(-50%, -50%);
              z-index: 1;
              width: 392px;
              height: 239px;
              border: 4px solid #fcbd3b;

              @media screen and (min-width: 500px) and (max-width: 767px) {
                top: 49%;
                height: 175px;
                width: 62%;
              }
              @media screen and (min-width: 320px) and (max-width: 500px) {
                top: 55%;
                height: 170px;
                width: 62%;
              }
            }

            .am-schema_item__big {
              z-index: 2;
              width: 215px;
              height: 176px;
              background: $yellow;
              border-radius: 10px;
              box-shadow: $grey1 6px 6px;
              margin-bottom: 40px;
              position: relative;

              @media screen and (min-width: 500px) and (max-width: 767px) {
                width: 180px;
                height: 130px;
              }

              @media screen and (min-width: 400px) and (max-width: 500px) {
                width: 169px;
                height: 113px;
              }

              @media screen and (min-width: 320px) and (max-width: 400px) {
                width: 139px;
                height: 113px;
              }

              img {
                @media screen and (min-width: 500px) and (max-width: 767px) {
                  transform: scale(0.7);
                }
                @media screen and (min-width: 320px) and (max-width: 500px) {
                  transform: scale(0.7);
                  margin-bottom: -10px;
                }
              }
            }
            .am-schema_inner__bottom {
              justify-content: space-around;
              .am-schema_item__small {
                width: 144px;
                height: 176px;
                background: $yellow;
                z-index: 2;
                border-radius: 10px;
                box-shadow: $grey1 6px 6px;
                @media screen and (min-width: 500px) and (max-width: 767px) {
                  width: 105px;
                  height: 114px;
                  box-shadow: $grey1 4px 4px;
                }

                @media screen and (min-width: 320px) and (max-width: 500px) {
                  width: 80px;
                  height: 99px;
                  box-shadow: $grey1 3px 3px;
                }

                h5 {
                  font: bold 14px niveau-grotesk;
                  line-height: 20px;
                  letter-spacing: 0.3px;
                  text-transform: uppercase;
                  text-align: center;
                  margin-top: 15px;
                  @media screen and (min-width: 500px) and (max-width: 767px) {
                    margin-top: -10px;
                  }
                  @media screen and (min-width: 320px) and (max-width: 500px) {
                    margin-top: 5px;
                  }
                }

                .image {
                  img {
                    height: 88px;
                    @media screen and (min-width: 500px) and (max-width: 767px) {
                      transform: scale(0.7);
                    }
                    @media screen and (min-width: 320px) and (max-width: 500px) {
                      transform: scale(0.7);
                      margin-bottom: -10px;
                      height: 70px;
                    }
                  }
                }
              }
            }
          }

          .list_inner {
            position: relative;
            padding-left: 70px;
            @media screen and (min-width: 400px) and (max-width: 1200px) {
              padding-left: 40px;
            }
            @media screen and (min-width: 320px) and (max-width: 400px) {
              padding-left: 4px;
            }
            h3 {
              padding-top: 36px;
              font: 900 34px niveau-grotesk;
              line-height: 48px;
              color: $grey1;

              @media (max-width: 767px) {
                font-size: 22.4px;
              }
            }
            .list {
              margin-top: 32px;
              padding-left: 25px;
              @media screen and (min-width: 320px) and (max-width: 550px) {
                margin-top: 0px;
              }
              .list_item {
                list-style: none;
                margin-bottom: 20px;
                @media screen and (min-width: 320px) and (max-width: 550px) {
                  margin-bottom: 5px;
                }
                position: relative;

                &:nth-child(1){
                  &:before{
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: -25px;
                    width: 20px;
                    height: calc(50% + 5px);
                    border-left: 3px solid #000;
                    border-top: 3px solid #000;
                    border-radius: 7px 0 0 0;
                    @media screen and (min-width: 551px) {
                      height: calc(50% + 20px);
                    }
                  }
                }

                &:nth-child(2),
                &:nth-child(3) {
                  &:after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: -25px;
                    transform: translateY(-50%);
                    width: 20px;
                    height: 3px;
                    background-color: #000000;
                    @media screen and (max-width: 550px) {
                      height: 2px;
                    }
                  }
                  &:before{
                    content: "";
                    position: absolute;
                    top: 0;
                    left: -25px;
                    width: 3px;
                    height: calc(100% + 5px);
                    border-left: 3px solid #000;
                    @media screen and (min-width: 551px) {
                      height: calc(100% + 20px);
                    }
                  }
                }

                &:last-child{
                  &:before{
                    content: "";
                    position: absolute;
                    top: 0;
                    left: -25px;
                    width: 20px;
                    height: 50%;
                    border-left: 3px solid #000;
                    border-bottom: 3px solid #000;
                    border-radius: 0 0 0 7px;
                  }
                }

                .list_img {
                  margin-right: 13px;
                  min-width: 20px;
                  align-self: center;
                }
                .list_title {
                  font: normal 22px niveau-grotesk;
                  line-height: 32px;
                  letter-spacing: 0.3px;
                  color: $grey1;

                  @media (max-width: 767px) {
                    font-size: 14.04px;
                  }
                }
              }
            }
            /*.list_line {
              position: absolute;
              top: 146px;
              bottom: 14.4%;
              left: 16.5%;
              width: 20px;
              border-radius: 7px 0 0 7px;
              border-left: 3px solid #000;
              border-top: 3px solid #000;
              border-bottom: 3px solid #000;

              @media screen and (min-width: 901px) and (max-width: 1200px) {
                left: 39px;
                top: 131px;
                bottom: 10.5%;
              }
              @media screen and (min-width: 769px) and (max-width: 901px) {
                left: 39px;
                top: 131px;
                bottom: 10.5%;
              }

              @media screen and (min-width: 550px) and (max-width: 769px) {
                left: 39px;
                top: 130px;
                bottom: 9%;
              }
              @media screen and (min-width: 400px) and (max-width: 550px) {
                left: 39px;
                top: 104px;
                bottom: 11%;
                border-left: 2px solid #000;
                border-top: 2px solid #000;
                border-bottom: 2px solid #000;
              }

              @media screen and (min-width: 358px) and (max-width: 400px) {
                left: 3px;
                top: 107px;
                bottom: 11%;
                border-left: 2px solid #000;
                border-top: 2px solid #000;
                border-bottom: 2px solid #000;
              }
              @media screen and (min-width: 320px) and (max-width: 358px) {
                left: 3px;
                top: 122px;
                bottom: 8%;
                border-left: 2px solid #000;
                border-top: 2px solid #000;
                border-bottom: 2px solid #000;
              }
            }*/
          }
        }

        &:after {
          content: " ";
          position: absolute;
          right: 0;
          top: 16px;
          z-index: -1;
          width: 1031px;
          height: 510px;
          background: $grey1;
          border-radius: 20px;
          @media screen and (max-width: 1200px) {
            width: 831px;
            right: 14%;
            height: 526px;
          }
          @media screen and (max-width: 1199px) {
            width: 831px;
            right: 14%;
            height: 100%;
          }

          @media screen and (min-width: 550px) and (max-width: 1190px) {
            height: 100%;
            width: 77%;
            margin: 0 auto;
            right: 8%;
          }
          @media screen and (min-width: 320px) and (max-width: 550px) {
            height: 100%;
            width: 99%;
            margin: 0 auto;
            right: -2%;
          }
        }
      }
    }
    .block-marker {
      position: absolute;
      width: 70px;
      height: 244px;
      background: $grey1;
      border-radius: 20px 0px 0px 20px;
      left: 0;
      top: 0;
      z-index: -1;

      @media screen and (min-width: 767px) and (max-width: 1200px) {
        left: 5%;
      }
      @media screen and (min-width: 550px) and (max-width: 767px) {
        left: 3%;
      }
      @media screen and (min-width: 320px) and (max-width: 550px) {
        left: 15%;
      }
      p {
        color: $white;
        white-space: nowrap;
        font: 900 14px niveau-grotesk;
        line-height: 143%;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        margin: 0;
        transform: rotate(-90deg) translateY(-5px);
      }
    }
  }
}

.reasonsarousel.reasonsarousel-integration {
    padding: 0;
}

.integration {
  position: relative;
  padding-bottom: 132px;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-bottom: 60px;
  }

  .integration_body {
    position: relative;
    padding-top: 162px;

    @media screen and (min-width: 320px) and (max-width: 767px) {
      padding-top: 60px;
    }

    h2 {
      font: 400 58px Playfair Display;
      color: $grey1;
      margin-bottom: 32px;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        padding: 10px;
        margin: 0 auto 10px;
        line-height: 1.25em;
        font-size: 22.4px;
        text-align: center;
      }

      br {
        @media screen and (min-width: 320px) and (max-width: 767px) {
          display: none;
        }
      }
    }
    .integration_description {
      font: 400 20px niveau-grotesk;
      line-height: 28px;
      letter-spacing: 0.3px;
      color: $grey1;
      margin-bottom: 79px;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        padding: 0px;
        line-height: 1.25em;
        font-size: 14.04px;
        margin: 0 auto 20px;
      }
    }
  }

  .integration_slider {
    margin-top: 0;
  }


    .integration_slide {
      position: relative;
      margin: 37px 0 0px;
      @media screen and (min-width: 320px) and (max-width: 767px) {
        margin: 20px auto 50px;
        width: 80%;
        height: auto;
        padding-bottom: 0px;
      }
      .block_technical {
        position: absolute;
        top: -22px;
        left: 55px;
        @media screen and (min-width: 767px) and (max-width: 992px) {
          left: 15px;
        }
        @media screen and (min-width: 320px) and (max-width: 767px) {
          left: 50%;
          transform: translateX(-50%) scale(0.7);
          top: -40px;
        }
      }

      h3 {
        padding: 121px 0 0 64px;
        font: 900 32px niveau-grotesk;
        line-height: 48px;
        text-transform: uppercase;
        color: #000000;
        @media screen and (min-width: 767px) and (max-width: 992px) {
          padding-left: 30px;
        }
        @media screen and (min-width: 320px) and (max-width: 767px) {
          text-align: center;
          padding: 100px 0 0 0;
          line-height: 1.25em;
          font-size: 22.4px;
        }
      }
      .integration_slide__text {
        font: 420 20px niveau-grotesk;
        line-height: 30px;
        letter-spacing: 0.3px;
        color: #000000;
        padding: 0 64px;
        margin: 16px auto 64px;
        @media screen and (min-width: 767px) and (max-width: 992px) {
          padding-left: 30px;
        }
        @media screen and (min-width: 320px) and (max-width: 767px) {
          padding: 0px;
          line-height: 1.25em;
          font-size: 14.04px;
          margin-bottom: 30px;
        }
      }
    }


    .integration_slider__indicators {
      width: 100%;
      height: 80px;
      background: #fc6839;
      border-radius: 0px 0px 20px 20px;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        height: 60px;
      }

      .slider__control {
        position: relative;
        cursor: pointer;
        text-decoration: none;
        text-transform: uppercase;
        font: 900 20px niveau-grotesk;
        line-height: 28px;
        letter-spacing: 0.3px;
        color: #ffffff;
        @media screen and (min-width: 320px) and (max-width: 767px) {
          line-height: 20px;
          font-size: 14.04px;
        }
      }
      .slider__control_prev {
        &::before {
          content: "";
          position: absolute;
          width: 48px;
          height: 3px;
          background: #ffffff;
          transform: translate(-50%, -50%);
          top: 50%;
          left: -35px;

          @media screen and (min-width: 320px) and (max-width: 767px) {
            width: 35px;
            height: 2px;
            left: -27px;
          }
        }
      }

      .slider__control_next {
        &::after {
          content: "";
          position: absolute;
          width: 48px;
          height: 3px;
          background: #ffffff;
          transform: translate(-50%, -50%);
          top: 50%;
          right: -85px;

          @media screen and (min-width: 320px) and (max-width: 767px) {
            width: 35px;
            height: 2px;
            right: -60px;
          }
        }
      }
    }
  }


.implement {
  position: relative;
  padding-bottom: 136px;
  background-color: $pink;

  @media (max-width: 767px) {
    padding-bottom: 36px;
  }
  h2 {
    font: normal 58px Playfair Display;
    line-height: 72px;
    text-align: center;
    text-transform: capitalize;
    color: $grey1;
    padding-top: 130px;
    width: 90%;
    margin: 0 auto 64px;

    @media (max-width: 767px) {
      font-size: 22.4px;
      line-height: 30px;
      width: 100%;
      margin: 0 auto 20px;
      padding-top: 70px;
    }
  }

  p {
    font: 400 24px niveau-grotesk;
    line-height: 32px;
    text-align: center;
    letter-spacing: 0.3px;
    color: $grey1;

    @media (max-width: 767px) {
      font-size: 14.04px;
      line-height: 20px;
      text-align: left;
      margin: 0 auto;
    }
  }

  .implement_body {
    position: relative;
    margin-top: 64px;
    @media screen and (min-width: 320px) and (max-width: 767px) {
      margin-top: 20px;
    }
    .caseMenu {
      margin: 30px auto 0;
      @media screen and (min-width: 767px) and (max-width: 1120px) {
        padding-left: 0;
      }
      @media screen and (min-width: 320px) and (max-width: 767px) {
        margin: 30px auto 45px;
        padding-left: 0;
      }
      .caseMenu_item {
        position: relative;
        width: 352px;
        height: 91px;
        list-style: none;
        border-bottom: none;
        @media screen and (min-width: 320px) and (max-width: 767px) {
          width: 80%;
          height: 69px;
          margin: 0 auto;
        }

        &:last-child {
          margin-bottom: 0;
        }

        .linkAnimate {
          font: normal 24px niveau-grotesk;
          line-height: 30px;
          letter-spacing: 0.3px;
          color: #333333;
          text-decoration: none;

          @media (max-width: 767px) {
            font-size: 14.04px;
            line-height: 20px;
          }
        }
      }

      .active {
        border-bottom: 2px solid #fc6839;
        border-top: 2px solid #fc6839;
        .linkAnimate {
          font-weight: bold;
        }

        .caseMenu_arrow {
          display: none;
        }
      }
    }

    .casearousel-implement {
      width: 100%;
      height: 100%;
      padding: 0;
      margin-top: 0;
      background-color: #ffffff;
      border-radius: 10px;

      .implement_item{

