            font-size: 14px;
          }
          
          @media (max-width: 767px) {
            font-size: 14.04px;
            margin: 20px auto 0;
            line-height: 20px;
            padding: 0 15px;
          }
        }
      }

      .active {
        display: flex;
      }
    }

    .image_poster {
      position: absolute;
      bottom: 0;
      left: 50px;
      @media screen and (min-width: 767px) and (max-width: 992px) {
        padding-left: 0px;
        left: -10px;
        bottom: 7%;
      }

      @media screen and (min-width: 320px) and (max-width: 767px) {
        position: relative;
        left: 0;
        display: flex;
        justify-content: center;
        width: 100%;
        margin-top: -30px;
      }

      .woman_photo {
        position: relative;
        z-index: 2;
        @media screen and (min-width: 767px) and (max-width: 992px) {
          transform: scale(0.8);
          left: 20px;
        }
        @media screen and (min-width: 320px) and (max-width: 767px) {
          transform: scale(0.7);
        }
      }
      .dots {
        position: absolute;
        z-index: 1;
        left: -20%;
        bottom: 10%;

        @media screen and (min-width: 320px) and (max-width: 767px) {
          left: 13%;
        }
      }
    }
  }
}

.connection {
  background-color: $pink;
  padding-bottom: 132px;
  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-bottom: 62px;
  }
  .connection_body {
    background: #fc6839;
    border-radius: 20px;
    position: relative;
    overflow: hidden;
    @media screen and (min-width: 767px) and (max-width: 1160px) {
      width: 99%;
      margin: 0 auto;
    }
    @media screen and (min-width: 320px) and (max-width: 767px) {
      width: 99%;
      margin: 0 auto;
    }

    h2 {
      font: 900 32px niveau-grotesk;
      line-height: 48px;
      color: #ffffff;
      width: 88%;
      padding: 80px 0 0 80px;
      @media screen and (min-width: 991px) and (max-width: 1000px) {
        padding: 80px 0 0 35px;
      }
      @media screen and (min-width: 767px) and (max-width: 991px) {
        padding: 80px 0 0 0;
        width: 90%;
        margin: 0 auto;
        text-align: center;
      }
      @media (max-width: 767px) {
        font-size: 20.4px;
        line-height: 30px;
        margin: 0 auto;
        width: 90%;
        text-align: center;
        padding: 40px 0 0;
      }
    }

    p {
      font: 500 20px niveau-grotesk;
      line-height: 28px;
      letter-spacing: 0.3px;
      color: #ffffff;
      padding-left: 80px;
      margin: 24px 0 40px;
      width: 85%;
      @media screen and (min-width: 991px) and (max-width: 1000px) {
        padding-left: 35px;
      }
      @media screen and (min-width: 767px) and (max-width: 991px) {
        padding-left: 35px;
        width: 53%;
      }
      @media screen and (min-width: 500px) and (max-width: 767px) {
        font-size: 14.04px;
        margin-top: 20px;
        line-height: 20px;
        padding-left: 35px;
        width: 100%;
        margin: 24px 0 30px;
      }
      @media screen and (min-width: 320px) and (max-width: 500px) {
        font-size: 14.04px;
        margin-top: 20px;
        line-height: 20px;
        padding-left: 15px;
        width: 100%;
        margin: 24px 0 30px;
      }
    }
    a {
      text-decoration: none;
      .link {
        cursor: pointer;
        width: 308px;
        height: 58px;
        box-shadow: 6px 6px #333;
        background: #ffffff;
        border: 4px solid $grey1;
        border-radius: 10px;
        font: 900 18px niveau-grotesk;
        line-height: 30px;
        text-align: center;
        text-transform: uppercase;
        color: $grey1;
        margin: 0 0 50px 80px;

        @media screen and (min-width: 767px) and (max-width: 1000px) {
          margin-left: 35px;
        }
        @media screen and (min-width: 500px) and (max-width: 767px) {
          font-size: 14.04px;
          margin-top: 20px;
          line-height: 20px;
          box-shadow: 3px 3px #333;
          margin-left: 35px;
          width: 180px;
          height: 38px;
        }

        @media screen and (min-width: 320px) and (max-width: 500px) {
          font-size: 14.04px;
          margin-top: 20px;
          line-height: 20px;
          box-shadow: 1px 1px #333;
          margin-left: 15px;
          width: 180px;
          height: 38px;
        }
      }
    }

    .image {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 45%;

      @media screen and (min-width: 767px) and (max-width: 992px) {
        width: 40%;
      }

      @media screen and (min-width: 500px) and (max-width: 767px) {
        position: relative;
        margin-left: auto;
        bottom: 0;
        max-height: 301px;
        width: auto;
      }

      @media screen and (min-width: 320px) and (max-width: 500px) {
        position: relative;
        margin-left: auto;
        width: 65%;
        max-height: 210px;
        width: auto;
      }
    }
  }
}

.protocols {
  background-color: $blue;
  padding-bottom: 143px;
  h2 {
    padding-top: 132px;
    margin-top: 0;

    @media screen and (min-width: 320px) and (max-width: 767px) {
      padding-top: 62px;
    }
  }
  .section_description {
    font: 420 24px niveau-grotesk;
    line-height: 28px;
    letter-spacing: 0.3px;
    color: $grey1;
    text-align: center;
    margin: 40px auto 74px;

    @media screen and (max-width: 767px) {
      line-height: 20px;
      font-size: 14.04px;
      margin: 10px auto 45px;
    }
  }

  .protocols_body {
    width: 100%;

    @media screen and (min-width: 320px) and (max-width: 500px) {
      flex-direction: column;
    }
    .protocols_item {
      position: relative;
      width: 20%;
      height: 138px;
      border-bottom: 1px solid #ffffff;
      border-right: 1px solid #ffffff;

      @media screen and (min-width: 500px) and (max-width: 767px) {
        height: 100px;
      }

      @media screen and (min-width: 320px) and (max-width: 500px) {
        width: 80%;
        border-right: none;
        margin: 0 auto;
        height: 85px;
      }

      &:last-child {
        border-right: none;
      }

      .protocols_code {
        font: 300 20px Fira Code;
        line-height: 32px;
        color: #ffffff;
        position: absolute;
        left: 16px;
        top: 0;

        @media screen and (min-width: 500px) and (max-width: 767px) {
          line-height: 20px;
          font-size: 14.04px;
        }

        @media screen and (min-width: 320px) and (max-width: 500px) {
          top: 11px;
          line-height: 20px;
          font-size: 14.04px;
        }
      }
      h4 {
        font: 900 24px niveau-grotesk;
        line-height: 28px;
        text-align: center;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: $grey1;

        @media screen and (max-width: 767px) {
          line-height: 20px;
          font-size: 14.04px;
          margin-top: 12px;
        }
      }
    }
  }
}
@media screen and (max-width: 962px) {
  .caseMenu_arrow-lg{
    display: none !important;
  }  
}

