.linkArticle{
    display: block;
    color: #333333;

    &:hover {
        text-decoration: none;
    
        .featuredArticle_title, .articelColumn_title, .CatArticle_title, .articleSimple_title {
            color: #FC6839;
        }
        .featuredArticle_desc,  .articelColumn_desc, .CatArticle_desc, .articleSimple_desc{
           
        }
    }
}


.featuredArticle {
    background: #F6F0E1;
    padding-top: 45px;
    padding-bottom: 48px;

    &_img {
        position: relative;
        padding-left: 20px;
        padding-bottom: 20px;

        &::after {
            position: absolute;
            width: 96%;
            content: "";
            top: 30px;
            border-radius: 50px 10px 10px 10px;
            bottom: 0;
            left: 0;
            right: 0;
            z-index: 1;
            background: #FC6839;
        }

        img {
            position: relative;
            z-index: 2;
            width: 100%;
            height: auto;
            border-radius: 10px 10px 50px 25px;
        }
        
    }

    &_inner{
        padding-top: 25px;
    }

    &_cat{
        display: block;
        font-weight: 900;
        font-size: 1em;
        padding-bottom: 25px;
    }

    &_title{ 
        font-family: Brandon Grotesque;
        border-bottom: 6px solid #333333;
        border-top: 6px solid #333333;
        text-transform: uppercase;
        padding: 15px 0px;
        font-weight: 900;
        font-size: 1.9em;
    }

    &_desc{
        padding-top: 25px;
        font-weight: 500;
    }

    &_letter{
        font-family: poynter-oldstyle-display;

        float: left;
        display: inline-block;
        padding-bottom: 5px;
        line-height: 0.8em;
        padding-right: 30px;
        text-transform: uppercase;
        font-size: 4.5em;
    }

    &-border{
        border-bottom: 3px solid #333333;
    }
}

.article-border{
    position: relative;

    &::after{
        content: "";
        position: absolute;
        bottom: -28px;
        height: 3px;
        width: calc(100% - 30px);
        left: 15px;
        
        background: #333333;
    }

    &-f {
        &::after {
           
            bottom: -48px;
        }
    }
}

.CatArticle{
    background-color: #F6F0E1;
    padding: 25px 0;

    &_img{
        margin-bottom: 25px;
        max-height: 301px;
        border-radius: 10px;
        overflow: hidden;

        img {
            border-radius: 10px;
            width: 100%;
            height: auto;
        }
    }

    &_title{
        font-family: Brandon Grotesque;
        margin-bottom: 25px;
        font-weight: 900;
        font-size: 1.1em;
    }

    &_desc{
        font-weight: 500;
        margin-bottom: 0;
    }

    &_inner{
        position: relative;

        .linkArticle:last-child{
            .articelColumn{
                margin-bottom: 0;
            }
        }

        &::after{
            content: "";
            left: -17px;
            position: absolute;
            top: 0;
            bottom: 0;
            width: 3px;
            background-color: #333333;
        }
    }
}


.articelColumn{
    display: flex;
    justify-content: space-between;
    margin-bottom: 33px;

    &_img{
        overflow: hidden;
        border-radius: 5px;
        max-width: 160px;
        max-height: 136px;

        img {
            width: 100%;
            height: auto;
        }
    }

    &_wrapp{
        width: 70%;
    }

    &_title{
        font-family: Brandon Grotesque;
        margin-bottom: 8px;
        font-weight: 900;
        font-size: 1.1em;
    }

    &_desc{
        font-weight: 500;
    }
}

.articleBlog { 
    background-color: #F6F0E1;
    padding-bottom: 60px;
}

.featuredArticle_desc::first-letter{
    font-size: 0em;
    line-height: 0em;
}

.articleSimple{
    margin-top: 28px;

    &_img{
        position: relative;
        overflow: hidden;
        max-height: 300px;
        border-radius: 10px;
        
        img{
           width: 100%;
           height: auto;
        }
    }

    &_title{
        font-family: Brandon Grotesque;
        margin-bottom: 15px;
        margin-top: 20px;
        font-weight: 900;
        font-size: 1.1em;        
    }

    &_desc{
        font-weight: 500;
        margin-bottom: 0;
    }
}

@media (max-width: 960px){
    .featuredArticle_title{
        font-size: 2em;
    }

    .featuredArticle_cat {
        font-size: 0.8em;
    }

    .featuredArticle{
        padding-top: 25px;
    }

    .articelColumn_wrapp{
        padding-left: 15px;
    }
}

@media (max-width: 770px){
    .CatArticle_left {
        display: flex;
        margin-bottom: 25px;
    }
    .CatArticle_title, .articelColumn_title{
        font-size: 18px;
    }

    .CatArticle_img {
        max-width: 160px;
    }

    .CatArticle-flex {
        width: 70%;
        padding-left: 15px;
    }

    .articleSimple_img {
        margin-bottom: 15px;
    }

    .articleSimple{
        margin-top: 20px;
       
    }

    .featuredArticle_letter {
        padding-right: 5px;
    }

}

@media (max-width: 540px){
    .articleSimple_img{
        max-height: 140px;
    }
}
